import { connect } from 'react-redux';
import * as perf from 'src/utils/perf';
import * as reporter from 'src/utils/reporter';
import * as selectors from '../reducers/selectors';
import ChatContent from '../components/ChatSection/ChatContent/ChatContent';
import * as actions from '../actions/chat';
import { getSecureFormContent } from '../services/secureForm';
import { isChattingWithAgent } from '../utils/common';

const mapStateToProps = (state) => {
  const chatId = selectors.getSelectedChatId(state);
  if (!chatId) return { };
  perf.markBegin('ChatContent: mapStateToProps');
  const chat = selectors.getChat(state, chatId);
  let g2mStartUrl = chat.get('g2mStartUrl');
  const visitor = selectors.getVisitorByChatId(state, chat && chat.get('id'));
  const secureFormServer = selectors.getSecureFormServer(state);
  const siteId = selectors.getSiteId(state);
  const visitorId = selectors.getVisitorIdByChatId(state, selectors.getSelectedChatId(state));
  const agentSession = selectors.getCurrentAgentSession(state);
  const timezone = selectors.getTimezone(state);
  const timezoneOffset = selectors.getTimezoneOffset(state);
  const textDirectionIfRTL = selectors.textDirectionIfRTL(state);
  const errorMessageForChat = selectors.getErrorMessageForChat(state, chat && chat.get('id'));
  const ifShowNotify = chat.get('ifShowNotify');

  if (!isChattingWithAgent(visitor, selectors.getCurrentAgentId(state))) {
    g2mStartUrl = null;
  }

  const ret = ({
    id: chat.get('id'),
    g2mStartUrl,
    siteId,
    visitorId,
    agentSession,
    selectedFontSize: selectors.getFontSize(state),
    messages: selectors.getChatMessages(state, selectors.getSelectedChatId(state)),
    typingMessage: chat.get('typingMessage'),
    chatTo: visitor.get('latestName'),
    secureFormServer,
    timezone,
    timezoneOffset,
    textDirectionIfRTL,
    errorMessageForChat,
    scrollTop: chat.get('scrollTop'),
    ifShowNotify,
  });
  perf.markEnd('ChatContent: mapStateToProps');
  return ret;
};

const mergeProps = (stateProps, dispatchProps) => {
  const { dispatch } = dispatchProps;

  const dispatchObject = {

    getSecureFormContent(token, rsaKey) {
      const config = {
        siteId: stateProps.siteId,
        visitorId: stateProps.visitorId,
        accessToken: token,
        authToken: stateProps.agentSession,
      };
      getSecureFormContent(stateProps.secureFormServer, config, rsaKey)
        .then((content) => {
          dispatch(actions.onGetSecureFormContent(stateProps.id, token, content));
        })
        .catch((error) => {
          reporter.error(error, 'ChatContent', 67, 0);
        });
    },
    onScroll(id, scrollTop) {
      dispatch(actions.updateScrollTop(id, scrollTop));
    },
    hideErrorMessage() {
      dispatch(actions.setErrorMessageForChat(stateProps.id, ''));
    },
    clearG2mStartUrl() {
      dispatch(actions.clearG2mStartUrl(stateProps.id));
    },
    updateIfShowNotify(ifShowNotify) {
      dispatch(actions.updateIfShowNotify(stateProps.id, ifShowNotify));
    },
  };
  return {
    ...stateProps,
    ...dispatchObject,
  };
};

export default connect(mapStateToProps, null, mergeProps)(ChatContent);



