import React from 'react';
import classnames from 'classnames';
import style from './TextArea.css';


const TextArea = (props) => {
  const { input, rows, cols, isDisabled, placeholder, type, maxLength, className, meta } = props;
  return (
    <textarea
      {...input}
      cols={cols}
      rows={rows}
      disabled={isDisabled ? 'disabled' : false}
      className={classnames(
        className,
        style.multipleText,
        meta.touched && meta.invalid && style.error
      )}
      type={type}
      placeholder={placeholder}
      maxLength={maxLength}
    >
    </textarea>
  );
};

export default TextArea;

TextArea.propTypes = {
  rows: React.PropTypes.string,
  cols: React.PropTypes.string,
  isDisabled: React.PropTypes.bool,
  placeholder: React.PropTypes.string,
  type: React.PropTypes.string,
  className: React.PropTypes.string,
  maxLength: React.PropTypes.number,
  input: React.PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
  meta: React.PropTypes.object.isRequired, // eslint-disable-line react/forbid-prop-types
};



