import React, { PropTypes } from 'react';
import _ from 'lodash';
import styles from './ChatItem.css';

class ChatItemSegment extends React.Component {
  shouldComponentUpdate(nextProps) {
    return !_.isEqual(this.props, nextProps);
  }
  render() {
    let height = 10;
    if (this.props.chatItem.segments.length <= 2) {
      height = 15;
    }
    return (
      <div className={styles.chatItemSegment}>
        {this.props.ifEnableSegmentation && this.props.chatItem.segments.map((segment, i) => (
          <i
            key={`segment_${i}`}
            data-tip={segment.name}
            data-for={this.props.dataFor}
            data-place="right"
            style={{ backgroundColor: segment.color, height }}
          />
        ))}
      </div>
    );
  }
}
ChatItemSegment.propTypes = {
  chatItem: PropTypes.shape({
    segments: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string,
      color: PropTypes.string,
    })).isRequired,
  }).isRequired,
  dataFor: PropTypes.string,
  ifEnableSegmentation: PropTypes.bool.isRequired,
};
ChatItemSegment.examples = {
  'Default Example': {
    description: 'chat segment',
    component:
      <ChatItemSegment />
        ,
  },
};
export default ChatItemSegment;



