import React from 'react';
// import classnames from 'classnames';

// import ReactModal from 'react-modal';
import Modal from '../Modal';
import ModalStyle from '../Modal.css';
import RadioButton from '../../RadioButton/RadioButton';
import Button from '../../Button/Button';
import InputStyle from '../../Input/Input.css';
import Input from '../../Input/Input';
import Link from 'src/components/Link/Link';

class ModalAttachTicket extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      attachType: '0',
      ifAttachNewTicket: true,
      ifAttachTicketButtonEnabled: true,
      ticketId: '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.changeTicketId = this.changeTicketId.bind(this);
    this.handleAttachTicket = this.handleAttachTicket.bind(this);
    this.hideResponseErrorMessage = this.hideResponseErrorMessage.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.responseErrorMessage) {
      if (this.timeoutId !== undefined) {
        clearTimeout(this.timeoutId);
      }
      this.timeoutId = setTimeout(this.hideResponseErrorMessage, 4000);
    }
  }
  handleChange(event) {
    switch (event.target.value) {
      case '0': {
        this.setState({
          attachType: '0',
          ifAttachNewTicket: true,
          ifAttachTicketButtonEnabled: true,
        });
        break;
      }
      case '1': {
        this.setState({
          attachType: '1',
          ifAttachNewTicket: false,
          ifAttachTicketButtonEnabled: false,
        });
        break;
      }
      default: break;
    }
  }
  changeTicketId(event) {
    this.setState({
      ifAttachTicketButtonEnabled: event.target.value,
      ticketId: event.target.value,
    });
  }
  handleAttachTicket(attachTicketId) {
    this.props.handleAttachTicket(this.props.visitorId, attachTicketId);
    this.setState({
      ifAttachTicketButtonEnabled: false,
    });
  }
  hideResponseErrorMessage() {
    this.props.hideResponseErrorMessage();
  }
  render() {
    const inputElement = (
      <Input
        meta={{}}
        input={{ onChange: this.changeTicketId, value: this.state.ticketId }}
        className={InputStyle.attachTicket}
        isDisabled={this.state.ifAttachNewTicket}
      />
    );
    let attachTicketId = '';
    if (this.state.ifAttachNewTicket) {
      attachTicketId = 0;
    } else {
      attachTicketId = this.state.ticketId;
    }
    return (
      <Modal
        closeModal={this.props.closeModal}
        title="Attach to Ticket"
        actions={
          !this.props.ticketId ? {
            okProps: { text: 'OK', disabled: !this.state.ifAttachTicketButtonEnabled, eventClick: () => { this.handleAttachTicket(attachTicketId); } },
            cancelProps: { text: 'Cancel' },
          }
            :
            {
              cancelProps: { text: 'Close' },
            }
        }
      >
        <div className={ModalStyle.modalAttachtTicket}>
          {
            !this.props.ticketId ?
            (
              <div>
                <RadioButton
                  name="attachTicket"
                  radioOption={
                    [
                      {
                        text: 'A new ticket.',
                        value: '0',
                        checked: this.state.attachType === '0',
                      },
                      {
                        text: `An existing ticket Id: S${this.props.siteId}-T`,
                        value: '1',
                        checked: this.state.attachType === '1',
                        component: inputElement,
                      },
                    ]
                  }
                  handleChange={this.handleChange}
                />
              </div>
            )
            :
            (
              <div>
                <p>
                  You have attached a ticket with id: {this.props.ticketId}
                </p>
                <Link
                  href="#"
                  onClick={this.props.gotoAttachTicket}
                >
                  &gt;&gt; Go to the ticket
                </Link>
              </div>
            )
          }
          {
            this.props.responseErrorMessage ?
              <div className={ModalStyle.errorContainer}>
                <span className={ModalStyle.message}>{this.props.responseErrorMessage}</span>
              </div>
              :
              ''
          }
        </div>
      </Modal>
    );
  }
}

export default ModalAttachTicket;

ModalAttachTicket.propTypes = {
  // ifOpen: React.PropTypes.bool.isRequired,
  closeModal: React.PropTypes.func.isRequired,
  siteId: React.PropTypes.number.isRequired,
  visitorId: React.PropTypes.number.isRequired,
  ticketId: React.PropTypes.string.isRequired,
  handleAttachTicket: React.PropTypes.func.isRequired,
  gotoAttachTicket: React.PropTypes.func,
  responseErrorMessage: React.PropTypes.string,
  hideResponseErrorMessage: React.PropTypes.func,
};



