import React from 'react';
import classnames from 'classnames';
import Button from 'src/components/Button/Button';
import style from '../Icon.css';
import chatActionStyle from '../../ChatSection/ChatAction/ChatAction.css';
import Icon from '../../Icon/Icon';
import { icons as enums } from '../../../constants/enums';
import PromptAutoTranslate from '../../Prompt/AutoTranslate/AutoTranslate';


class Translate extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: props.autoTranslateInfo.ifOpenAutoTranslation,
    };
    this.togglePrompt = this.togglePrompt.bind(this);
    this.closePrompt = this.closePrompt.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    this.setState({
      isOpen: nextProps.autoTranslateInfo.ifOpenAutoTranslation,
    });
  }
  togglePrompt() {
    this.setState({
      isOpen: !this.state.isOpen,
    });
  }
  closePrompt() {
    this.setState({
      isOpen: false,
    });
    if (!this.props.autoTranslateInfo.ifAutoPopTranslation) {
      this.props.onUpdateIfPopTranslation(this.props.chatId);
    }
  }

  render() {
    return (
      <div
        ref={(element) => { this.container = element; }}
      >
        <Button
          type="noborder"
          className={
            classnames(
              chatActionStyle.chatActionIcon,
              !this.props.ifEnable && chatActionStyle.chatActionIconDisabled,
              this.state.isOpen && chatActionStyle.iconActive
            )
          }
          onClick={this.props.ifEnable ? this.togglePrompt : undefined}
          data-tip="Automatically translate chat messages to a target language in real time."
          data-for="chatContainer"
        >
          <Icon type={enums.translate} />
        </Button>
        {
          this.state.isOpen &&
            <PromptAutoTranslate
              closePrompt={this.closePrompt}
              visitorId={this.props.visitorId}
              detectedVisitorLanguage={this.props.autoTranslateInfo.detectedVisitorLanguage}
              onToggleAutoTranslate={this.props.onToggleAutoTranslate}
            />
        }
      </div>
    );
  }
}

export default Translate;

Translate.propTypes = {
  ifEnable: React.PropTypes.bool,
  visitorId: React.PropTypes.number,
  chatId: React.PropTypes.number,
  onToggleAutoTranslate: React.PropTypes.func.isRequired,
  autoTranslateInfo: React.PropTypes.shape({
    detectedVisitorLanguage: React.PropTypes.string,
    ifOpenAutoTranslation: React.PropTypes.bool,
    ifAutoPopTranslation: React.PropTypes.bool,
  }),
  onUpdateIfPopTranslation: React.PropTypes.func,
};



