import React from 'react';
import GrayButton from '../../Button/Button';
import chatHeaderStyle from '../ChatHeader/ChatHeader.css';

import ModalLeaveChat from '../../Modal/ModalLeaveChat/ModalLeaveChat';

class LeaveChat extends React.Component {
  constructor() {
    super();
    this.state = {
      open: false,
    };
    this.onLeaveChat = this.onLeaveChat.bind(this);
    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }
  onLeaveChat() {
    this.props.onLeaveChat(this.props.visitorId);
    this.closeModal();
  }
  closeModal() {
    this.setState(
      { open: false }
    );
  }
  openModal() {
    this.setState(
      { open: true }
    );
  }
  render() {
    return (
      <div>
        <GrayButton
          text="Leave Chat"
          className={chatHeaderStyle.btnAction}
          onClick={this.openModal}
        />
        {this.state.open && <ModalLeaveChat
          openModal={this.openModal}
          closeModal={this.closeModal}
          onLeaveChat={this.onLeaveChat}
        />}
      </div>
    );
  }
}

export default LeaveChat;

LeaveChat.propTypes = {
  visitorId: React.PropTypes.number.isRequired,
  onLeaveChat: React.PropTypes.func.isRequired,
};



