import { connect } from 'react-redux';
import * as perf from 'src/utils/perf';
import ChatItemContainer from '../components/ChatItem/ChatItemContainer';
import * as chatListActionCreater from '../actions/chatList';
import { acceptChat,
  refuseChat,
  acceptTransfer,
  setPreference,
  refuseTransfer } from '../actions/agent';
import { createChatListState } from './Business/chatList';
import { chatListConfig, chatStatus } from '../constants/enums';
import * as infoActions from '../actions/visitorDetails';
import * as salesforceActions from '../actions/salesforce';
import * as actionTypes from '../constants/types/chatList';
import * as chatActionCreater from '../actions/chat';
import { operateGroupArrow, switchConfirmBeforeCloseWindow } from '../actions/ui';
import * as localStorage from '../utils/localStorage';

let mapStateToProps = state => createChatListState(state);

const mapDispatchToProps = dispatch => ({
  onClickConfig(code) {
    if (code === chatListConfig.showUnreposiveTime) {
      dispatch(chatListActionCreater.createConfigAction(code, actionTypes.showUnreposiveTime));
    } else if (code === chatListConfig.showDepartment) {
      dispatch(chatListActionCreater.createConfigAction(code, actionTypes.showDepartment));
    } else if (code === chatListConfig.showByStatus) {
      dispatch(chatListActionCreater.createConfigAction(code, actionTypes.showByStatus));
    }
    dispatch(setPreference());
  },
  onClickPin(visitor) {
    dispatch(chatListActionCreater.togglePin(visitor.chatId));
  },
  onItemClick(visitor) {
    dispatch(chatListActionCreater.setSelected(visitor.chatId));
    dispatch(infoActions.resetForms());
    dispatch(salesforceActions.reset());
    localStorage.deleteItem('salesforceFormState');
    localStorage.deleteItem('prechatFormState');
    localStorage.deleteItem('customFields_FormState');
  },
  onAcceptChat(visitor) {
    if (visitor.currentStatus === chatStatus.transferring) {
      dispatch(acceptTransfer(visitor.visitorId));
    } else {
      dispatch(acceptChat(visitor.visitorId));
    }
    dispatch(chatListActionCreater.setSelected(visitor.chatId));
  },
  onRefusedChat(visitor) {
    if (visitor.currentStatus === chatStatus.transferring) {
      dispatch(refuseTransfer(visitor.visitorId));
    } else {
      dispatch(refuseChat(visitor.visitorId));
    }
    dispatch(chatActionCreater.tryCloseChat(visitor.chatId));
  },
  onWrapUp(visitor) {
    dispatch(chatListActionCreater.setSelected(visitor.chatId));
    dispatch(chatListActionCreater.wrapUp(visitor.visitorId));
  },
  onDeleteItem(visitor) {
    if (visitor.status === chatStatus.manuallyInvitedAndWatingVisitor) {
      dispatch(switchConfirmBeforeCloseWindow(true,
      'ManuallyInvited',
      'Are you sure you want to cancel the invitation?', {
        visitorId: visitor.visitorId,
      }));
    } else {
      dispatch(chatActionCreater.tryCloseChat(visitor.chatId));
    }
  },
  onClickArrow(id) {
    dispatch(operateGroupArrow(id));
  },
});

if (process.env.NODE_ENV !== 'production') {
  mapStateToProps = perf.markFunctionCall('ChatList: mapStateToProps', mapStateToProps);
}

export default connect(mapStateToProps, mapDispatchToProps)(ChatItemContainer);



