import React, { PropTypes } from 'react';
import styles from './ChatItem.css';

const NoChat = props => (
  <div className={styles.noChatSection}>
    <div>
      <h1>You have no chats now.</h1>
      <span
        onClick={props.goToVisitors}
      >
        Go to Visitor list
      </span> to invite a visitor to chat
    </div>
  </div>
);
NoChat.propTypes = {
  goToVisitors: PropTypes.func.isRequired,
};
export default NoChat;




