class AutoTranslate extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedLanguageValue:
        props.detectedVisitorLanguage === '0' ? '20' : props.detectedVisitorLanguage,
    };
    this.handleChangeLanguage = this.handleChangeLanguage.bind(this);
    this.handleTurnOnAutoTranslate = this.handleTurnOnAutoTranslate.bind(this);
    this.handleTurnOffAutoTranslate = this.handleTurnOffAutoTranslate.bind(this);
  }
  handleChangeLanguage(value) {
    this.setState({
      selectedLanguageValue: value,
    });
  }
  handleTurnOnAutoTranslate() {
    this.props.closePrompt();
    this.props.onToggleAutoTranslate(
      turnOnAutoTranslate,
      this.props.visitorId,
      this.state.selectedLanguageValue
    );
  }
  handleTurnOffAutoTranslate() {
    this.props.closePrompt();
    this.props.onToggleAutoTranslate(
      turnOffAutoTranslate,
      this.props.visitorId,
      this.state.selectedLanguageValue
    );
  }
  render() {
    return (
      <div className={classnames(PromptStyle.promptWindow, autoTranslateStyle.autoTranslate)}>
        <button
          className={PromptStyle.close}
          onClick={this.props.closePrompt}
        >
          <Icon type={icons.close} size={10} />
        </button>
        <h3>Auto Translation</h3>
        <span>Detected visitor language: </span>
        <Select
          options={languages}
          width={170}
          input={{
            value: this.state.selectedLanguageValue,
            onChange: this.handleChangeLanguage,
          }}
          dropdownClassName={PromptStyle.dropdown}
          customizeStyle={{ top: 10, marginLeft: 5 }}
        />
        <div className={PromptStyle.action}>
          <Button
            type="primary"
            text="Turn On"
            onClick={this.handleTurnOnAutoTranslate}
          />
          <Button type="default" text="Turn Off" onClick={this.handleTurnOffAutoTranslate} />
        </div>
      </div>
    );
  }
}


export default AutoTranslate;

AutoTranslate.propTypes = {
  // ifOpen: React.PropTypes.bool.isRequired,
  closePrompt: React.PropTypes.func.isRequired,
  visitorId: React.PropTypes.number,
  onToggleAutoTranslate: React.PropTypes.func.isRequired,
  detectedVisitorLanguage: React.PropTypes.string,
};





