import React from 'react';
import classnames from 'classnames';
import style from './Icon.css';
import Icon from './Icon.jsx';

const requestFlag = (countryCode) => {
  try {
    return require(`./images/flag/${countryCode}.svg`);
  } catch (e) {
    return null;
  }
};

const Country = (props) => {
  const tooltip = `${props.city}, ${props.state}, ${props.countryName}`;
  if (!props.countryCode) return null;
  const nameLowerCase = props.countryCode.toLowerCase();
  const imgCountry = requestFlag(nameLowerCase);
  if (imgCountry === null) return null;
  return (
    <span data-tip={tooltip} data-for={props['data-for'] || 'chatContainer'}>
      <Icon
        className={classnames(style.iconSmaller, style.iconOffsetDown)}
        style={{ backgroundImage: `url(${imgCountry})` }}
      />
    </span>
  );
};



export default Country;

Country.propTypes = {
  countryName: React.PropTypes.string,
  city: React.PropTypes.string,
  state: React.PropTypes.string,
  countryCode: React.PropTypes.string,
  'data-for': React.PropTypes.string,
};



