import { connect } from 'react-redux';
import { PromoteToUser, PromoteToContact } from 'src/constants/enums/promote';
import * as perf from 'src/utils/perf';
import * as userType from 'src/constants/enums/userType';
import * as permissionType from 'src/constants/enums/liveChatPermission';
import * as helper from 'src/utils/common';
import * as agentAction from '../actions/agent';
import ChatHeader from '../components/ChatSection/ChatHeader/ChatHeader';
import * as selectors from '../reducers/selectors';
import * as chatActionCreater from '../actions/chat';
import * as visitorStatus from '../constants/enums/visitorStatus';
import * as uiActionCreater from '../actions/ui';
import { getSFObjectId } from '../components/Tabs/Salesforce/util'; // @todo place it elsewhere

const ifDisplayPromote = (state) => {
  const chat = selectors.getSelectedChat(state);
  const visitor = selectors.getVisitorByChatId(state, chat && chat.get('id'));
  const siteInfo = selectors.getSiteInfo(state);
  const ifHavePromotePermission = selectors.getPermission(state, permissionType.promoteVisitor);
  if (visitor.get('identityType') !== userType.User &&
  visitor.get('identityType') !== userType.Contact &&
  (siteInfo.ifContactOpened && siteInfo.ifUserOpened) && ifHavePromotePermission) {
    return true;
  }
  return false;
};

const ifDisplayBan = (state) => {
  const siteInfo = selectors.getSiteInfo(state);
  const ifHaveBanPermission = selectors.getPermission(state, permissionType.manageBan);
  if (siteInfo.ifCanBan && ifHaveBanPermission) {
    return true;
  }
  return false;
};

const ifDisplayLeaveChat = (state) => {
  const chat = selectors.getSelectedChat(state);
  if (!chat) return false;
  const visitor = selectors.getVisitorByChatId(state, chat.get('id'));
  const currentAgentId = selectors.getCurrentAgentId(state);
  const isMonitoring = helper.isMonitorWithAgent(visitor, currentAgentId);
  const isChattingWithAgent = selectors.checkIfVisitorStatusChatting(visitor.get('status')) &&
      helper.isAgentInChatActiveList(visitor, currentAgentId);

  if (isMonitoring || isChattingWithAgent) {
    return true;
  }
  return false;
};

const isDisplayCloseChat = (state) => {
  const chat = selectors.getSelectedChat(state);
  if (!chat) return false;
  const visitor = selectors.getVisitorByChatId(state, chat.get('id'));
  const agentId = selectors.getCurrentAgentId(state);

  const status = visitor.get('status');
  const isMonitoring = helper.isChatting(visitor) &&
      helper.isMonitorWithAgent(visitor, agentId);

  const isChatEnded = selectors.isChatEnded(status) ||
      (helper.isVisitorChattedWithAgent(visitor, agentId) &&
      !(helper.isChatting(visitor) && helper.isAgentInChatActiveList(visitor, agentId)));
  const isTransferring = status === visitorStatus.transferring;

  const isMonitorEnded = status === visitorStatus.chatEnded &&
    helper.isAgentInMonitorList(visitor, agentId);

  return (isChatEnded && !isMonitoring && !isTransferring) ||
  isMonitorEnded || (status === visitorStatus.manuallyInvitedAndWaitingVisitor);
};

const getSalesforceURL = (state, visitor) => {
  const salesforceInteConfig = state.config.salesforce.integrationConfig;
  if (!salesforceInteConfig) {
    return '';
  }
  const instanceUrl = salesforceInteConfig.instanceUrl;
  if (!instanceUrl) return '';
  if (visitor.get('salesforceContactFields') != null && visitor.get('salesforceContactFields').length) {
    return `https://${instanceUrl.replace('https://', '').replace('http://', '')}/${getSFObjectId(visitor.get('salesforceContactFields'))}`;
  } else if (visitor.get('salesforceAccountFields') != null && visitor.get('salesforceAccountFields').length) {
    return `https://${instanceUrl.replace('https://', '').replace('http://', '')}/${getSFObjectId(visitor.get('salesforceAccountFields'))}`;
  } else if (visitor.get('salesforceLeadFields') != null && visitor.get('salesforceLeadFields').length) {
    return `https://${instanceUrl.replace('https://', '').replace('http://', '')}/${getSFObjectId(visitor.get('salesforceLeadFields'))}`;
  }
  return '';
};

const getIsContactOrUserOpen = (state) => {
  if (state.config.settings.siteInfo) {
    return (state.config.settings.siteInfo.ifContactOpened &&
    state.config.settings.siteInfo.ifUserOpened);
  }
  return false;
};

const mapStateToProps = (state) => {
  const chatId = selectors.getSelectedChatId(state);
  if (!chatId) {
    return { };
  }
  perf.markBegin('ChatHeader: mapStateToProps');
  const chat = selectors.getChat(state, chatId);
  const visitor = selectors.getVisitorByChatId(state, chat && chat.get('id'));

  const saleforceURL = getSalesforceURL(state, visitor);
  const checkChatExisted = chat.get('id') !== visitor.get('chatId');
  const isVisitorExists = selectors.isVisitorExists(state, visitor.get('id'));

  const isContactOrUserOpen = getIsContactOrUserOpen(state);
  const ret = ({
    isContactOrUserOpen,
    chatId: selectors.getSelectedChatId(state),
    visitorInfo: visitor ? {
      avatar: visitor.get('avatar'),
      identityType: visitor.get('identityType'),
      latestName: visitor.get('latestName'),
      country: visitor.get('country'),
      city: visitor.get('city'),
      state: visitor.get('state'),
      countryCode: visitor.get('countryCode'),
      operatingSystem: visitor.get('operatingSystem'),
      browser: visitor.get('browser'),
      zendeskUrl: visitor.get('zendeskUrl'),
      socialType: visitor.get('socialType'),
      socialUrl: visitor.get('socialUrl'),
      id: visitor.get('id'),
      prechatEmail: visitor.get('prechatEmail'),
      realVisitorId: visitor.get('realVisitorId'),
      ip: visitor.get('ip'),
      status: visitor.get('status'),
    } : { },
    saleforceURL,
    disabledPromote: checkChatExisted || !isVisitorExists,
    disabledBan: checkChatExisted || !isVisitorExists,
    chatHeaderToolBarDisplay: {
      displayPromote: ifDisplayPromote(state),
      displayBan: ifDisplayBan(state),
      displayLeave: ifDisplayLeaveChat(state),
      displayCloseChat: isDisplayCloseChat(state),
    },
    ifUseMainServer: selectors.ifUseMainServer(state),
  });
  perf.markEnd('ChatHeader: mapStateToProps');
  return ret;
};

const mapDispatchToProps = dispatch => ({
  onPromote(visitorId, promoteType) {
    switch (promoteType) {
      case PromoteToUser: {
        dispatch(agentAction.promoteToUser(visitorId));
        break;
      }
      case PromoteToContact: {
        dispatch(agentAction.promoteToContact(visitorId));
        break;
      }
      default: break;
    }
  },
  onBanChat(visitorId, banInfo) {
    dispatch(agentAction.banVisitor(visitorId, banInfo));
  },
  onLeaveChat(visitorId) {
    dispatch(agentAction.leaveChat(visitorId));
  },
  onCloseChat(chatId, status, visitorId) {
    if (status === visitorStatus.manuallyInvitedAndWaitingVisitor) {
      dispatch(uiActionCreater.switchConfirmBeforeCloseWindow(true,
      'ManuallyInvited',
      'Are you sure you want to cancel the invitation?', {
        visitorId,
      }));
    } else {
      dispatch(chatActionCreater.tryCloseChat(chatId));
    }
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(ChatHeader);



