import React from 'react';
import classnames from 'classnames';
import { dateToString } from 'src/utils/time';
import Link from 'src/components/Link/Link';
import * as messageType from '../../../constants/enums/messageType';
import * as str from '../../../utils/str';
import style from './ChatContent.css';

import Icon from '../../Icon/Icon';
import { secureFormStatus, icons } from '../../../constants/enums';

const text = (message) => {
  if (message.translatedMessage && message.translatedMessage !== '') {
    let translateMsg = '';
    let orignalMsg = '';
    translateMsg = str.handleLinks(message.translatedMessage, (content, i) => {
      const link = content.indexOf('www.') === 0 ? `http://${content}` : content;
      return <Link href={link} key={i} newWindow>{content}</Link>;
    });
    orignalMsg = str.handleLinks(message.message, (content, i) => {
      const link = content.indexOf('www.') === 0 ? `http://${content}` : content;
      return <Link href={link} key={i} newWindow>{content}</Link>;
    });
    return (
      <p key={message.id}>
        {translateMsg} <i>(Translation)</i><br />
        <span className={style.translationMsg}>{orignalMsg}</span> <i>(Original)</i>
      </p>

    );
    // buildedMessage = `${translateMsg} <i>(Translation)</i>\r\n<i>${orignalMsg} (Original)</i>`;
  }
  return (
    <p key={message.id}>
      { str.handleLinks(message.message, (content, i) => {
        const link = content.indexOf('www.') === 0 ? `http://${content}` : content;
        return <Link href={link} key={i} newWindow>{content}</Link>;
      })}
    </p>
  );
};
const image = message => (
  message.url ? (
    <div key={message.id}>
      <Link key={message.id} href={message.url} newWindow>
        <img src={message.url} alt={message.filename} style={{ maxWidth: 400, maxHeight: 400 }} />
      </Link>
    </div>
  ) : null
);
const file = message => (
  <div key={message.id}>
    <Link key={message.id} className={style.attachment} href={message.url} newWindow>
      {message.fileName}
    </Link>
  </div>
);

const getMessageComponent = (message) => {
  if (message.message) return text(message);
  if (message.url) {
    if (message.isImage) return image(message);
    return file(message);
  }
  return null;
};

class ChatContentMsg extends React.Component {
  componentDidMount() {
    const firstMessageObj = this.props.messages[0];
    if (firstMessageObj.type === messageType.secureForm &&
      firstMessageObj.message.status === secureFormStatus.init) {
      this.props.getSecureFormContent(firstMessageObj.message.token,
        firstMessageObj.message.rsaKey);
    }
  }
  shouldComponentUpdate(nextProps) {
    return nextProps.messages !== this.props.messages;
  }
  render() {
    const messages = this.props.messages;
    const name = messages[0].sender;
    const time = dateToString(
      messages[0].time,
      'HH:mm',
      this.props.timezone,
      this.props.timezoneOffset
    );
    const type = messages[0].type;
    if (type === messageType.system) {
      return <div className={classnames(style.systemMeg)}>{messages[0].message}</div>;
    }
    return (
      <div
        className={classnames(
          style.chatContentInfo,
          (type === messageType.agent) ? style.operator : style.visitor
        )}
      >
        {
          type === messageType.secureForm ?
            <div className={style.secureForm}>
              {
                (messages[0].message.status === secureFormStatus.downloading ||
                  messages[0].message.status === secureFormStatus.init) ?
                    <Icon type={icons.loading} size="small" /> :
                    messages[0].message.content.map(item => (
                      <div>
                        {item.name}: {item.value}
                      </div>
                    ))
              }
            </div>
            :
            <div>
              <div>
                <span className={style.name}>{`${name}: `}</span>
                <span className={style.time}>{time}</span>
              </div>
              <div className={style.chatMeg}>
                {messages.map(message => getMessageComponent(message))}
              </div>
            </div>
        }
      </div>
    );
  }
}

export default ChatContentMsg;

ChatContentMsg.propTypes = {
  messages: React.PropTypes.oneOfType([
    React.PropTypes.shape({}),
    React.PropTypes.arrayOf(React.PropTypes.shape({})),
  ]),
  getSecureFormContent: React.PropTypes.func,
  timezone: React.PropTypes.number,
  timezoneOffset: React.PropTypes.number,
};



