import React from 'react';
import * as _ from 'lodash';
import classnames from 'classnames';
import ModalStyle from '../Modal.css';
import Icon from '../../Icon/Icon';
import Select from '../../Select/Select';
import { icons, agentStatus } from '../../../constants/enums';
// import Link from 'src/components/Link/Link';
import Button from 'src/components/Button/Button';

class SelectAgentByDepartmentId extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      selectedDepartmentValue: -1,
      allAvailableAgentsByDepartmentId: props.allAvailableAgents,
    };
    this.handleChangeDepartment = this.handleChangeDepartment.bind(this);
    this.getAvailableAgentsByDepartmentId = this.getAvailableAgentsByDepartmentId.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    if (!_.isEqual(nextProps.allAvailableAgents, this.props.allAvailableAgents)) {
      this.setState({
        // selectedDepartmentValue: -1,
        allAvailableAgentsByDepartmentId:
          this.getAvailableAgentsByDepartmentId(this.state.selectedDepartmentValue,
          nextProps.allAvailableAgents),
      });
    }
  }
  getAvailableAgentsByDepartmentId(departmentId, allAvailableAgents) {
    const self = this.props;
    let allAvailableAgentsByDepartmentId = [];
    if (departmentId !== -1) {
      const onlineDepartmentById = self.onlineDepartments.filter(onlineDepartment => (
        onlineDepartment.id === departmentId
      ));
      if (onlineDepartmentById.length > 0) {
        onlineDepartmentById[0].agentIds.forEach((agentId) => {
          const filterAvailableAgent = allAvailableAgents.filter(allAvailableAgent => (
            allAvailableAgent.id === agentId
            && (allAvailableAgent.status === agentStatus.online
              || allAvailableAgent.status === agentStatus.away || allAvailableAgent.status >= 100)
          ));
          if (filterAvailableAgent.length > 0) {
            allAvailableAgentsByDepartmentId.push(filterAvailableAgent[0]);
          }
        });
      }
    } else {
      allAvailableAgentsByDepartmentId = allAvailableAgents;
    }
    return allAvailableAgentsByDepartmentId;
  }
  handleChangeDepartment(value) {
    this.setState({
      selectedDepartmentValue: value,
      allAvailableAgentsByDepartmentId:
        this.getAvailableAgentsByDepartmentId(value, this.props.allAvailableAgents),
    });
  }
  render() {
    return (
      this.props.allAvailableAgents.length ?
        <div className={ModalStyle.agentContainer}>
          {this.props.ifDepartmentEnabled && (
            <div className={ModalStyle.selectDepartment}>
              <Select
                input={{
                  value: this.state.selectedDepartmentValue,
                  onChange: this.handleChangeDepartment,
                }}
                options={this.props.onlineDepartmentOptions}
                width={350}
                dropdownClassName={ModalStyle.dropdown}
              />
            </div>
          )}
          <ul className={classnames(ModalStyle.availableList, ModalStyle.agentlist)}>
            {
              this.state.allAvailableAgentsByDepartmentId.map((item, index) => (
                <li key={index}>
                  <Button
                    type="noborder"
                    className={this.props.selectedAgentIndex === index ? ModalStyle.active : ''}
                    onClick={() => this.props.handleSelectAgent(item.id, index)}
                  >
                    {
                      item.status === agentStatus.online ?
                        <Icon
                          type={icons.statusOnline}
                          size={16}
                          color="#329fd9"
                        /> :
                        <Icon
                          type={icons.statusAway}
                          size={16}
                          color="#fe8e14"
                        />
                    }
                    <div className={ModalStyle.transferName}>
                      {item.name}
                    </div>
                  </Button>
                </li>
              ))
            }
          </ul>
        </div>
        :
        <p>There are no other agents available.
        You cannot transfer the ongoing chat to another agent.</p>
    );
  }
}

export default SelectAgentByDepartmentId;

SelectAgentByDepartmentId.propTypes = {
  ifDepartmentEnabled: React.PropTypes.bool,
  allAvailableAgents: React.PropTypes.arrayOf(React.PropTypes.shape({})),
  onlineDepartmentOptions: React.PropTypes.array,
  onlineDepartments: React.PropTypes.arrayOf(React.PropTypes.shape({})),
  handleSelectAgent: React.PropTypes.func,
  selectedAgentIndex: React.PropTypes.number,
};



