import React, { PropTypes } from 'react';
import * as _ from 'lodash';
import classnames from 'classnames';
import styles from './ChatItem.css';
import ShowChatViaList from './ShowChatViaList';
import Icon from '../Icon/Icon';
import { icons } from '../../constants/enums';

class ShowChatViaGroup extends React.Component {
  constructor(props) {
    super(props);
    this.timer = null;
    this.clickArrow = this.clickArrow.bind(this);
  }
  shouldComponentUpdate(nextProps) {
    return !_.isEqual(this.props, nextProps);
  }
  clickArrow(id) {
    this.props.onClickArrow(id);
  }
  render() {
    return (
      <div className={styles.showChatViaGroup}>
        {this.props.groupItems.filter(item => item.chats.length > 0)
          .map(item => (
            <div
              key={item.id}
              className={styles.groupSection}
            >
              <div
                className={classnames(styles.groupNameWraper, item.opened && styles.opened)}
                onClick={() => this.clickArrow(item.id)}
              >
                <Icon type={icons.arrowDown} className={styles.groupSpread} />
                <span>
                  {`${item.description}(${item.chats.length})`}
                </span>
              </div>
              {
                item.opened && (
                  <ShowChatViaList
                    chats={item.chats}
                    enableRefuseChat={this.props.enableRefuseChat}
                    ifEnableSegmentation={this.props.ifEnableSegmentation}
                    isShowUnreposiveTime={this.props.isShowUnreposiveTime}
                    isShowDepartment={this.props.isShowDepartment}
                    onClickPin={this.props.onClickPin}
                    onItemClick={this.props.onItemClick}
                    onAcceptChat={this.props.onAcceptChat}
                    onRefusedChat={this.props.onRefusedChat}
                    onWrapUp={this.props.onWrapUp}
                    onDeleteItem={this.props.onDeleteItem}
                  />
                )
              }
            </div>
        ))}
      </div>
    );
  }
}

ShowChatViaGroup.propTypes = {
  groupItems: PropTypes.arrayOf(PropTypes.shape({
    description: PropTypes.string.isRequired,
    chats: PropTypes.arrayOf(PropTypes.shape({})).isRequired,
    opened: PropTypes.bool,
  })).isRequired,
  isShowUnreposiveTime: PropTypes.bool.isRequired,
  isShowDepartment: PropTypes.bool.isRequired,
  ifEnableSegmentation: PropTypes.bool,
  enableRefuseChat: PropTypes.bool,
  onClickPin: PropTypes.func.isRequired,
  onItemClick: PropTypes.func.isRequired,
  onAcceptChat: PropTypes.func.isRequired,
  onRefusedChat: PropTypes.func.isRequired,
  onWrapUp: PropTypes.func.isRequired,
  onDeleteItem: PropTypes.func.isRequired,
  onClickArrow: PropTypes.func.isRequired,
};
export default ShowChatViaGroup;



