import React from 'react';

import classnames from 'classnames';
import _ from 'lodash';
import commonStyle from '../../CommonResources/common.css';
import chatHeaderStyle from './ChatHeader.css';

import Avatar from '../../Icons/Avatar';
import Country from '../../Icons/Country';
import Os from '../../Icons/OS';
import Browser from '../../Icons/Browser';
import Social from '../../Icons/Social';
import Promote from '../Promote/Promote';
import BanChat from '../BanChat/BanChat';
import LeaveChat from '../LeaveChat/LeaveChat';
import CloseChat from '../CloseChat/CloseChat';

import { Zendesk, Salesforce } from '../../../constants/enums/socialType';

class ChatHeader extends React.Component {
  shouldComponentUpdate(nextProps) {
    return !_.isEqual(this.props, nextProps);
  }
  render() {
    return (
      <div className={classnames(chatHeaderStyle.chatHeader, commonStyle.clearfix)}>
        <div className={classnames(chatHeaderStyle.visitorDetails, commonStyle.clearfix)}>
          <div className={chatHeaderStyle.avatar}>
            <Avatar
              isContactOrUserOpen={this.props.isContactOrUserOpen}
              avatarLink={this.props.visitorInfo.avatar}
              identityType={this.props.visitorInfo.identityType}
            />
          </div>
          <div className={chatHeaderStyle.details}>
            <div className={chatHeaderStyle.name}>
              {this.props.visitorInfo.latestName}
            </div>
            <div className={classnames(chatHeaderStyle.others, commonStyle.clearfix)}>
              <Country
                countryName={this.props.visitorInfo.country}
                city={this.props.visitorInfo.city}
                state={this.props.visitorInfo.state}
                countryCode={this.props.visitorInfo.countryCode}
              />
              <Os name={this.props.visitorInfo.operatingSystem} />
              <Browser name={this.props.visitorInfo.browser} />
              {
                this.props.saleforceURL !== '' && <Social
                  type={Salesforce}
                  socialUrl={this.props.saleforceURL}
                />
              }
              {
                this.props.visitorInfo.zendeskUrl !== '' && <Social
                  type={Zendesk}
                  socialUrl={this.props.visitorInfo.zendeskUrl}
                />
              }
              {
                this.props.visitorInfo.socialType > 0 && <Social
                  type={this.props.visitorInfo.socialType}
                  socialUrl={this.props.visitorInfo.socialUrl}
                />
              }
            </div>
          </div>
        </div>
        <div className={chatHeaderStyle.action}>
          {
            this.props.chatHeaderToolBarDisplay.displayPromote && <Promote
              onPromote={this.props.onPromote}
              visitorId={this.props.visitorInfo.id}
              preChatEmail={this.props.visitorInfo.prechatEmail}
              disabled={this.props.disabledPromote}
              ifUseMainServer={this.props.ifUseMainServer}
            />
          }
          {
            this.props.chatHeaderToolBarDisplay.displayBan && <BanChat
              onBanChat={this.props.onBanChat}
              realVisitorId={this.props.visitorInfo.realVisitorId}
              visitorId={this.props.visitorInfo.id}
              visitorIP={this.props.visitorInfo.ip}
              disabled={this.props.disabledBan}
            />
          }
          {
            this.props.chatHeaderToolBarDisplay.displayLeave && <LeaveChat
              visitorId={this.props.visitorInfo.id}
              onLeaveChat={this.props.onLeaveChat}
            />
          }
          {
            this.props.chatHeaderToolBarDisplay.displayCloseChat && <CloseChat
              chatId={this.props.chatId}
              visitorId={this.props.visitorInfo.id}
              status={this.props.visitorInfo.status}
              onCloseChat={this.props.onCloseChat}
            />
          }
        </div>

      </div>
    );
  }
}

export default ChatHeader;

ChatHeader.propTypes = {
  chatId: React.PropTypes.number.isRequired,
  visitorInfo: React.PropTypes.shape({
    avatar: React.PropTypes.string,
    latestName: React.PropTypes.string,
    prechatEmail: React.PropTypes.string,
    country: React.PropTypes.string,
    city: React.PropTypes.string,
    state: React.PropTypes.string,
    countryCode: React.PropTypes.string,
    operatingSystem: React.PropTypes.string,
    browser: React.PropTypes.string,
    id: React.PropTypes.number,
    realVisitorId: React.PropTypes.number,
    ip: React.PropTypes.string,
    socialType: React.PropTypes.number,
    socialUrl: React.PropTypes.string,
    status: React.PropTypes.number,
    zendeskUrl: React.PropTypes.string,
    identityType: React.PropTypes.number,
  }),
  saleforceURL: React.PropTypes.string,
  disabledPromote: React.PropTypes.bool,
  disabledBan: React.PropTypes.bool,
  isContactOrUserOpen: React.PropTypes.bool,
  chatHeaderToolBarDisplay: React.PropTypes.object,
  onPromote: React.PropTypes.func.isRequired,
  onBanChat: React.PropTypes.func.isRequired,
  onLeaveChat: React.PropTypes.func.isRequired,
  onCloseChat: React.PropTypes.func.isRequired,
  ifUseMainServer: React.PropTypes.bool.isRequired,
};



