import React from 'react';
// import classnames from 'classnames';
import _ from 'lodash';

import PromptStyle from '../Prompt.css';
import Button from '../../Button/Button';
import RadioButton from '../../RadioButton/RadioButton';
import Icon from '../../Icon/Icon';
import { icons } from '../../../constants/enums';

class PromptSecureForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      pciFormId: props.pciForm.pciFormList.length ? props.pciForm.pciFormList[0].id.toString() : '0',
    };
    this.handleChange = this.handleChange.bind(this);
  }
  handleChange(event) {
    this.setState({
      pciFormId: event.target.value,
    });
  }
  render() {
    const pciFormList = [];
    if (this.props.pciForm.pciFormList.length) {
      this.props.pciForm.pciFormList.forEach((item) => {
        pciFormList.push({
          value: item.id.toString(),
          text: item.name,
        });
      });
    }

    const selectedPCIForm = _.find(this.props.pciForm.pciFormList, pciForm => (
      pciForm.id === parseInt(this.state.pciFormId, 10)
    ));
    return (
      <div className={PromptStyle.promptWindow}>
        <button
          className={PromptStyle.close}
          onClick={this.props.closePrompt}
        >
          <Icon type={icons.close} size={10} />
        </button>
        <h3>Request PCI Form</h3>
        {
          pciFormList.length ?
          (
            <RadioButton
              name="pciforms"
              radioOption={pciFormList}
              selectedVal={this.state.pciFormId.toString()}
              handleChange={this.handleChange}
            />
          )
          :
          (
            <p>
              You have no PCI Forms.
            </p>
          )
        }
        <div className={PromptStyle.action}>
          <Button
            type="primary"
            text="OK"
            onClick={
                selectedPCIForm ?
                  () => {
                    this.props.onRequestPCIForm(
                      this.props.pciForm.secureFormServer,
                      selectedPCIForm,
                      this.props.pciForm.secureFormConfig,
                      this.props.visitorId,
                      this.props.chatId,
                      selectedPCIForm.name,
                    );
                    this.props.closePrompt();
                  }
                  :
                  this.props.closePrompt
            }
          />
          <Button type="default" text="Cancel" onClick={this.props.closePrompt} />
        </div>
      </div>
    );
  }
}


export default PromptSecureForm;

PromptSecureForm.propTypes = {
  closePrompt: React.PropTypes.func.isRequired,
  pciForm: React.PropTypes.shape({
    secureFormServer: React.PropTypes.string,
    secureFormConfig: React.PropTypes.object,
    pciFormList: React.PropTypes.array,
  }),
  visitorId: React.PropTypes.number.isRequired,
  chatId: React.PropTypes.number.isRequired,
  onRequestPCIForm: React.PropTypes.func.isRequired,
};





