import * as _ from 'lodash';
import axios from 'axios';
import { createGUID } from '../utils/guid';

/**
 * @param {string} serverPath - secure form server path, https://secureForm.comm100.com
 * @param {object} secureForm - secure form object
 * @param {object} config
 * config:
 * {
 *  siteId: 10000,
 *  visitorId: 1,
 *  authToken: '[sessionId]',
 *  authProvider: 'chatserver.comm100.com',
 *  themeColor: '#ff0000',
 * }
 * @returns {Promise} -
 */

export const requestSecureForm = (serverPath, secureForm, config) =>
  new Promise((resolve) => {
    require.ensure(['node-rsa'], (require) => {
      resolve(require('node-rsa'));
    }, 'secure-form');
  }).then(NodeRSA =>
    new Promise((resolve, reject) => {
      const rsaKey = new NodeRSA({ b: 512 }, null, {
        environment: 'browser',
        encryptionScheme: 'pkcs1',
      });
      const token = createGUID();
      const url = `${serverPath}/formbuilder.ashx`;
      const data = JSON.stringify(
        Object.assign(
          {},
          config,
          {
            accessToken: token,
            publicKey: rsaKey.exportKey('pkcs8-public-pem'),
            formName: secureForm.name,
            jsonFormFields: JSON.stringify(secureForm.fields),
          }
        )
      );
      axios.post(url, data)
        .then((res) => {
          if (res.data.success) {
            resolve({ token, rsaKey });
          } else {
            reject(res.error_message);
          }
        })
        .catch((err) => {
          reject(err);
        });
    })
  );

/**
 * @param {string} serverPath - secure form server path, https://secureForm.comm100.com
 * @param {object} config
 * {
 *  siteId: 10000,
 *  visitorId: 1,
 *  accessToken: token,
 *  authToken: '[sessionId]'
 * }
 * @param {NodeRSA} rsaKey -
 * @returns {Promise} -
 */
export const getSecureFormContent = (serverPath, config, rsaKey) =>
  new Promise((resolve, reject) => {
    const url = `${serverPath}/formconsumer.ashx`;
    const data = JSON.stringify(config);
    axios.post(url, data)
      .then((res) => {
        const { error_code: errorCode, error_message: errorMessage, formContent } = res.data;
        if (errorCode === 0) {
          const baseLength = 88;  // (512/48 + 1) * 8;
          const encryptTexts = _.chunk(formContent, baseLength).map(chars => chars.join(''));
          const bufferContent = Buffer.concat(encryptTexts.map(texts => rsaKey.decrypt(texts)));
          const encodedContent = new Buffer(bufferContent).toString('utf8');
          const content = new Buffer(encodedContent, 'base64').toString();
          const fields = JSON.parse(content).fields;
          resolve(fields);
        } else {
          reject(errorMessage);
        }
      })
      .catch((err) => {
        reject(err);
      });
  });



