import React from 'react';
import classnames from 'classnames';
import Button from 'src/components/Button/Button';
import chatActionStyle from '../../ChatSection/ChatAction/ChatAction.css';
import Icon from '../../Icon/Icon';
import { icons as enums } from '../../../constants/enums';
import PromptSecureForm from '../../Prompt/PromptSecureForm/PromptSecureForm';

class SecureForm extends React.Component {
  constructor() {
    super();
    this.state = {
      isOpenSecureForm: false,
    };
    this.toggleOpenPrompt = this.toggleOpenPrompt.bind(this);
    this.closeOpenPrompt = this.closeOpenPrompt.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }
  componentDidMount() {
    document.addEventListener('click', this.handleClick, false);
  }
  componentWillUnmount() {
    document.removeEventListener('click', this.handleClick, false);
  }
  handleClick(event) {
    const container = this.container;
    if (container && !container.contains(event.target) && this.state.isOpenSecureForm) {
      this.setState({ isOpenSecureForm: false });
    }
  }
  toggleOpenPrompt() {
    this.setState({
      isOpenSecureForm: !this.state.isOpenSecureForm,
    });
  }
  closeOpenPrompt() {
    this.setState({
      isOpenSecureForm: false,
    });
  }
  render() {
    return (
      <div ref={(element) => { this.container = element; }}>
        <Button
          type="noborder"
          className={
            classnames(
              chatActionStyle.chatActionIcon,
              !this.props.ifEnable && chatActionStyle.chatActionIconDisabled,
              this.state.isOpenSecureForm && chatActionStyle.iconActive,
            )
          }
          onClick={this.props.ifEnable ? this.toggleOpenPrompt : undefined}
          data-tip="Request a PCI form from your visitor."
          data-for="chatContainer"
        >
          <Icon type={enums.secureForm} />
        </Button>
        {
          this.state.isOpenSecureForm &&
            <PromptSecureForm
              closePrompt={this.closeOpenPrompt}
              visitorId={this.props.visitorId}
              chatId={this.props.chatId}
              pciForm={this.props.pciForm}
              onRequestPCIForm={this.props.onRequestPCIForm}
            />
        }
      </div>
    );
  }
}


export default SecureForm;

SecureForm.propTypes = {
  ifEnable: React.PropTypes.bool,
  ifDisplay: React.PropTypes.bool,
  visitorId: React.PropTypes.number.isRequired,
  chatId: React.PropTypes.number,
  pciForm: React.PropTypes.object.isRequired,
  onRequestPCIForm: React.PropTypes.func.isRequired,
};



