import React from 'react';
import GrayButton from '../../Button/Button';
import chatHeaderStyle from '../ChatHeader/ChatHeader.css';

import ModalBanChat from '../../Modal/ModalBanChat/ModalBanChat';

class BanChat extends React.Component {
  constructor() {
    super();
    this.state = {
      open: false,
    };
    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }
  closeModal() {
    this.setState(
      { open: false }
    );
  }
  openModal() {
    this.setState(
      { open: true }
    );
  }
  render() {
    return (
      <div>
        <GrayButton
          text="Ban"
          className={chatHeaderStyle.btnAction}
          onClick={this.openModal}
          disabled={this.props.disabled}
        />
        {this.state.open && <ModalBanChat
          openModal={this.openModal}
          closeModal={this.closeModal}
          onBanChat={this.props.onBanChat}
          realVisitorId={this.props.realVisitorId}
          visitorId={this.props.visitorId}
          visitorIP={this.props.visitorIP}
        />}
      </div>
    );
  }
}

export default BanChat;


BanChat.propTypes = {
  onBanChat: React.PropTypes.func.isRequired,
  realVisitorId: React.PropTypes.number,
  visitorId: React.PropTypes.number,
  visitorIP: React.PropTypes.string,
  disabled: React.PropTypes.bool,
};



