import React from 'react';
import classnames from 'classnames';
import Button from 'src/components/Button/Button';
import chatActionStyle from '../../ChatSection/ChatAction/ChatAction.css';
import Icon from '../../Icon/Icon';
import { icons as enums } from '../../../constants/enums';

import ModalAttachTicket from '../../Modal/ModalAttachTicket/ModalAttachTicket';
import ModalStandbyNotice from '../../Modal/StandbyNotice/StandbyNotice';

const dataFor = 'chatContainer';
const dataTip = 'Attach this chat to a ticket.';

class AttachTicket extends React.Component {
  constructor() {
    super();
    this.state = {
      open: false,
      dataFor,
      dataTip,
      isOpenModalStandbyNotice: false,
    };
    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.closeStandbyWindow = this.closeStandbyWindow.bind(this);
  }


  closeModal() {
    /** @todo why is dataFor and dataTip useful here? */
    this.setState(
      { open: false, dataFor, dataTip }
    );
  }
  closeStandbyWindow() {
    this.setState({
      isOpenModalStandbyNotice: false,
      dataFor,
      dataTip,
    });
  }
  openModal() {
    if (this.props.ifUseMainServer) {
      this.setState(
        { open: true, dataFor: '', dataTip: '' }
      );
    } else {
      this.setState(
        { isOpenModalStandbyNotice: true, dataFor: '', dataTip: '' }
      );
    }
  }

  render() {
    return (
      <div>
        <Button
          type="noborder"
          className={classnames(
            chatActionStyle.chatActionIcon,
            !this.props.ifEnable && chatActionStyle.chatActionIconDisabled
          )}
          onClick={this.props.ifEnable ? this.openModal : undefined}
          data-tip={this.state.dataTip}
          data-for={this.state.dataFor}
        >
          <Icon type={enums.attachticket} />
        </Button>
        {this.state.open && <ModalAttachTicket
          closeModal={this.closeModal}
          siteId={this.props.siteId}
          visitorId={this.props.visitorId}
          ticketId={this.props.ticketId}
          handleAttachTicket={this.props.handleAttachTicket}
          gotoAttachTicket={this.props.gotoAttachTicket}
          responseErrorMessage={this.props.responseErrorMessage}
          hideResponseErrorMessage={this.props.hideResponseErrorMessage}
        />}
        {this.state.isOpenModalStandbyNotice && <ModalStandbyNotice
          closeModal={this.closeStandbyWindow}
          text="Live Chat is running on the secondary server. We're sorry but you're
                unable to perform this action now. Please try again after Live Chat
                goes back to the primary server."
        />}
      </div>
    );
  }
}

export default AttachTicket;

AttachTicket.propTypes = {
  ifEnable: React.PropTypes.bool,
  siteId: React.PropTypes.number.isRequired,
  visitorId: React.PropTypes.number.isRequired,
  ticketId: React.PropTypes.string.isRequired,
  handleAttachTicket: React.PropTypes.func.isRequired,
  gotoAttachTicket: React.PropTypes.func,
  ifUseMainServer: React.PropTypes.bool.isRequired,
  responseErrorMessage: React.PropTypes.string,
  hideResponseErrorMessage: React.PropTypes.func,
};



