import React, { PropTypes } from 'react';
import _ from 'lodash';
import styles from './ChatItem.css';
import Button from '../Button/Button';

class ChatItemDetail extends React.Component {
  shouldComponentUpdate(nextProps) {
    return !_.isEqual(this.props, nextProps);
  }
  render() {
    return (
      <div className={styles.chatItemDetail}>
        <div className={styles.visitorName}>
          {this.props.chatItem.visitor.name}
        </div>
        {this.props.chatItem.visitor.agentName &&
          (
          <div className={styles.visitorDepartment}>
            ({this.props.chatItem.visitor.agentName})
          </div>
        )}
        {this.props.isShowDepartment &&
          this.props.chatItem.visitor.department &&
          (
          <div className={styles.visitorDepartment}>
            ({this.props.chatItem.visitor.department})
          </div>
        )}
        {this.props.chatItem.isShowAcceptAndRefuse &&
            (
              <div className={styles.actioncontainer}>
                <Button
                  type="primary"
                  text="Accept"
                  onClick={(e) => {
                    e.stopPropagation();
                    this.props.onAcceptChat(this.props.chatItem);
                  }}
                  size={'small'}
                  className={styles.acceptBtn}
                />
                {this.props.enableRefuseChat && (<Button
                  text="Refuse"
                  onClick={(e) => {
                    e.stopPropagation();
                    this.props.onRefusedChat(this.props.chatItem);
                  }}
                  className={styles.refuseBtn}
                />)}
              </div>
            )}
        {this.props.chatItem.isShowWrapUp && (
          <Button
            type="primary"
            text="Wrap up"
            onClick={(e) => {
              e.stopPropagation();
              this.props.onWrapUp(this.props.chatItem);
            }}
            size={'small'}
            className={styles.wrapupbtn}
          />
        )}
      </div>
    );
  }
}
ChatItemDetail.propTypes = {
  chatItem: PropTypes.shape({
    visitor: PropTypes.shape({
      name: PropTypes.string,
      agentName: PropTypes.string,
      department: PropTypes.string,
    }).isRequired,
    isShowWrapUp: PropTypes.bool.isRequired,
    isShowAcceptAndRefuse: PropTypes.bool.isRequired,
  }),
  enableRefuseChat: PropTypes.bool.isRequired,
  isShowDepartment: PropTypes.bool.isRequired,
  onAcceptChat: PropTypes.func.isRequired,
  onRefusedChat: PropTypes.func.isRequired,
  onWrapUp: PropTypes.func.isRequired,
};
ChatItemDetail.examples = {
  'Default Example': {
    description: 'chat detail',
    component:
      <ChatItemDetail
        visitor={{
          name: 'Andy',
          ip: '0.0.0.0',
          department: 'department',
        }}
        showAcceptAndRefuse
      />
        ,
  },
};
export default ChatItemDetail;



