import React from 'react';
import _ from 'lodash';
import classnames from 'classnames';
import Immutable from 'immutable';
import * as fontSize from 'src/constants/enums/fontSize';
import style from './ChatContent.css';
import Link from '../../Link/Link';
import ChatContentMsg from './ChatContentMsg';
import { icons } from '../../../constants/enums';
import Icon from '../../Icon/Icon';
import Button from '../../Button/Button';


const getFontSizeClass = type => ({
  [fontSize.small]: style.chatContentsmall,
  [fontSize.middle]: style.chatContentmiddle,
  [fontSize.large]: style.chatContentlarge,
})[type];

const getVisitorForNotify = (messages, preVisitor = '') => {
  const tempArray = messages.last();
  if (!_.isArray(tempArray) || tempArray.length === 0) return '';
  const sender = tempArray[0].sender;
  if (!_.isEmpty(sender)) {
    return sender;
  }
  return preVisitor;
};
const getMessageForNotify = (messages, preMessage = '') => {
  const tempArray = messages.last();
  if (!_.isArray(tempArray) || tempArray.length === 0) return '';
  const sender = tempArray[0].sender;
  if (!_.isEmpty(sender)) {
    return tempArray[tempArray.length - 1].message ||
         tempArray[tempArray.length - 1].fileName;
  }
  return preMessage;
};

class ChatContent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      // ifShowNotify: props.ifShowNotify,
      ifShowGotoMeetingNotify: false,
    };
    this.update = this.update.bind(this);
    this.selectChatMessage = this.selectChatMessage.bind(this);
    this.closeGotoMeetingNotify = this.closeGotoMeetingNotify.bind(this);
    this.scrollBottom = this.scrollBottom.bind(this);
    this.getScrollHeight = this.getScrollHeight.bind(this);
    this.canShowNotify = false;
    this.scrollTop = props.scrollTop;
    this.timer = null;
    this.ifScrollBottom = false;
    this.notifyVisitor = getVisitorForNotify(props.messages);
    this.notifyMessage = getMessageForNotify(props.messages);
  }
  componentDidMount() {
    this.parent.scrollTop = this.getScrollHeight();
    this.parent.addEventListener('scroll', this.update, false);
    document.body.addEventListener('keydown', this.selectChatMessage, false);
    // document.onkeydown = (e) => {

    // };
  }
  componentWillReceiveProps(nextProps) {
    const propsMessage = nextProps.messages;
    this.notifyVisitor = getVisitorForNotify(propsMessage, this.notifyVisitor);
    this.notifyMessage = getMessageForNotify(propsMessage, this.notifyMessage);
    if (this.props.g2mStartUrl !== nextProps.g2mStartUrl && !this.state.ifShowGotoMeetingNotify) {
      this.setState({
        ifShowGotoMeetingNotify: true,
      });
    }
    this.scrollTop = nextProps.scrollTop;
  }
  shouldComponentUpdate(nextProps, nextState) {
    return nextProps.id !== this.props.id ||
      nextProps.selectedFontSize !== this.props.selectedFontSize ||
      nextProps.typingMessage !== this.props.typingMessage ||
      nextProps.textDirectionIfRTL !== this.props.textDirectionIfRTL ||
      nextProps.errorMessageForChat !== this.props.errorMessageForChat ||
      nextProps.g2mStartUrl !== this.props.g2mStartUrl ||
      nextProps.ifShowNotify !== this.props.ifShowNotify ||
      nextState.ifShowGotoMeetingNotify !== this.state.ifShowGotoMeetingNotify ||
      !_.isEqual(this.props.messages.toJS(), nextProps.messages.toJS());
  }

  componentDidUpdate(prevProps) {
    const propsMessage = this.props.messages;
    if (propsMessage !== prevProps.messages ||
      this.props.typingMessage !== prevProps.typingMessage) {
      this.parent.scrollTop = this.getScrollHeight();
    }
  }
  getScrollHeight() {
    return this.scrollTop === -1 ? this.parent.scrollHeight : this.scrollTop;
  }

  componentWillUnmount() {
    if (this.timer) {
      clearTimeout(this.timer);
      this.timer = null;
    }
    this.parent.removeEventListener('scroll', this.update);
    document.body.removeEventListener('keydown', this.selectChatMessage);
  }

  update(event) {
    this.scrollTop = event.target.scrollTop;
    const clientHeight = event.target.clientHeight;
    const messageScrollHeight = event.target.scrollHeight;
    this.canShowNotify = (this.scrollTop + clientHeight < messageScrollHeight - 20);
    if (!this.canShowNotify && this.props.ifShowNotify) {
      // this.setState({
      //   ifShowNotify: false,
      // });
      this.props.updateIfShowNotify(false);
    }
    if (!this.canShowNotify) {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
      this.ifScrollBottom = true;
      this.props.onScroll(this.props.id, -1);
    } else {
      this.ifScrollBottom = false;
    }
    if (this.props.onScroll && !this.ifScrollBottom) {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
      this.timer = setTimeout(() => {
        this.props.onScroll(this.props.id, this.scrollTop);
      }, 500);
    }
  }
  selectChatMessage(e) {
    if (e.ctrlKey &&
      (e.keyCode === 65 || e.keyCode === 97) &&
      (e.target === document.body || e.target === this.parent)) {
      if (document.selection) {
        const range = document.body.createTextRange();
        range.moveToElementText(this.parent);
        range.select();
      } else if (window.getSelection) {
        const windowSelection = window.getSelection();
        if (windowSelection.rangeCount > 0) {
          windowSelection.removeAllRanges();
        }
        const range = document.createRange();
        range.selectNode(this.parent);
        windowSelection.addRange(range);
      }
      e.preventDefault();
      e.stopPropagation();
    }
  }

  closeGotoMeetingNotify() {
    this.setState({
      ifShowGotoMeetingNotify: false,
    });
    this.props.clearG2mStartUrl();
  }

  scrollBottom() {
    this.parent.scrollTop = this.container.getBoundingClientRect().height;
    // this.setState({
    //   ifShowNotify: false,
    // });
    this.ifScrollBottom = true;
    this.props.onScroll(this.props.id, -1);
  }

  render() {
    const props = this.props;
    // let notifyVisitor = '';
    // let notifyMessage = '';
    // const propsLastMessage = props.messages.get(props.messages.size - 1);
    // if (this.state.ifShowNotify && props.messages.size > 0 &&
    //   propsLastMessage[0].type === messageType.visitor) {
    //   // const propsLastMessage = props.messages.get(props.messages.size - 1);
    //   notifyVisitor = propsLastMessage[0].sender;
    //   notifyMessage = propsLastMessage[propsLastMessage.length - 1].message ||
    //   propsLastMessage[propsLastMessage.length - 1].fileName;
    // }
    return (
      <div
        className={style.chatContentContainer}
      >
        <div
          className={
            classnames(
              style.chatContent,
              getFontSizeClass(props.selectedFontSize),
              props.textDirectionIfRTL ? style.directionRTL : '',
              'contentMenu'
            )
          }
          ref={(element) => { this.parent = element; }}
        >
          <div ref={(element) => { this.container = element; }}>
            {
              props.messages.map((message, i) => <ChatContentMsg
                key={i}
                messages={message}
                getSecureFormContent={this.props.getSecureFormContent}
                timezone={this.props.timezone}
                timezoneOffset={this.props.timezoneOffset}
              />)
            }
          </div>
          {
            props.typingMessage !== '' ?
              <div className={style.typingIndicator}>
                <span>{props.chatTo} is typing: </span>
                {props.typingMessage}...
                <Icon
                  type={icons.typing}
                  size={13}
                />
              </div>
              :
              null
          }
        </div>
        {this.props.ifShowNotify && this.props.scrollTop !== -1 && (
          <div className={style.notify} onClick={this.scrollBottom}>
            <span className={style.notifyName}>{this.notifyVisitor}:</span> {this.notifyMessage}
          </div>
        )}
        {
          (props.g2mStartUrl && this.state.ifShowGotoMeetingNotify) ?
            <div className={classnames(style.notify, style.notifyG2M)}>
              You have created a new meeting.
              <span className={style.action} onClick={this.closeGotoMeetingNotify}>
                <Link
                  href={props.g2mStartUrl}
                  newWindow
                >
                  Start Meeting Immediately
                </Link>
                {/** @todo not proper to use href="#", remove it */}
                <Button type="noborder">Not Now</Button>
              </span>
            </div>
            : ''
        }
        {
          props.errorMessageForChat ?
            <div className={style.error}>
              <span className={style.errorContent}>{props.errorMessageForChat}</span>
              {/** @todo not proper to use href="#", remove it */}
              <Button type="noborder" onClick={props.hideErrorMessage}>Dismiss</Button>
            </div>
            :
            ''
        }
      </div>
    );
  }
}

export default ChatContent;

ChatContent.propTypes = {
  id: React.PropTypes.number.isRequired,
  selectedFontSize: React.PropTypes.string.isRequired,
  messages: React.PropTypes.instanceOf(Immutable.List).isRequired,
  chatTo: React.PropTypes.string.isRequired,
  typingMessage: React.PropTypes.string,
  getSecureFormContent: React.PropTypes.func,
  timezone: React.PropTypes.number,
  timezoneOffset: React.PropTypes.number,
  textDirectionIfRTL: React.PropTypes.bool,
  g2mStartUrl: React.PropTypes.string,
  errorMessageForChat: React.PropTypes.string,
  clearG2mStartUrl: React.PropTypes.func,
  scrollTop: React.PropTypes.number,
  onScroll: React.PropTypes.func,
  ifShowNotify: React.PropTypes.bool,
  updateIfShowNotify: React.PropTypes.func,
};



