import * as _ from 'lodash';
import React, { PropTypes, Component } from 'react';
import styles from './ChatItem.css';
import PromptNumber from '../Icons/PromptNumber';
import Icon from '../Icon/Icon';
import { icons, chatStatus } from '../../constants/enums';
import { formatWaitingTimeWithMillsecond } from '../../utils/time';

const ifShowTime = (currentStatus, isShowUnresponsiveTime) => {
  if (currentStatus === chatStatus.waitingForChat ||
      currentStatus === chatStatus.transferring ||
      currentStatus === chatStatus.invitedAndWaitingForChat) {
    return true;
  }
  if (currentStatus === chatStatus.chatting && isShowUnresponsiveTime) {
    return true;
  }
  return false;
};

class ChatItemPrompt extends Component {
  constructor(props) {
    super(props);
    this.timer = null;
    this.unresponseBeginTime = props.chatItem.unresponseBeginTime;
    this.updateUnresponsiveTime = this.updateUnresponsiveTime.bind(this);
  }
  componentDidMount() {
    if (ifShowTime(this.props.chatItem.currentStatus, this.props.isShowUnreposiveTime)) {
      this.updateUnresponsiveTime();
    }
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.chatItem.unresponseBeginTime !== this.props.chatItem.unresponseBeginTime) {
      if (nextProps.chatItem.unresponseBeginTime === null) {
        clearTimeout(this.timer);
        this.timer = null;
        this.unresponseBeginTime = 0;
        this.timestamp.textContent = '';
      } else {
        this.unresponseBeginTime = nextProps.chatItem.unresponseBeginTime;
        if (ifShowTime(nextProps.chatItem.currentStatus, nextProps.isShowUnreposiveTime)) {
          this.updateUnresponsiveTime();
        }
      }
    }
  }

  shouldComponentUpdate(nextProps, nextState) {
    return !_.isEqual(this.state, nextState) ||
           !_.isEqual(this.props, nextProps);
  }

  componentDidUpdate(prevProps) {
    const shouldShow =
      ifShowTime(this.props.chatItem.currentStatus, this.props.isShowUnreposiveTime);
    const prevShouldShow =
      ifShowTime(prevProps.chatItem.currentStatus, prevProps.isShowUnreposiveTime);
    if (!prevShouldShow && shouldShow) {
      this.updateUnresponsiveTime();
    } else if (prevShouldShow && !shouldShow && this.timer) {
      clearTimeout(this.timer);
      this.timer = null;
      this.timestamp.textContent = '';
    }
  }

  componentWillUnmount() {
    if (this.timer !== null) {
      clearTimeout(this.timer);
      this.timer = null;
    }
  }

  updateUnresponsiveTime() {
    if (!this.unresponseBeginTime) {
      if (this.timer) {
        clearTimeout(this.timer);
        this.timer = null;
      }
      return;
    }
    const ticks = Date.now() - this.unresponseBeginTime;
    this.timestamp.textContent = ticks < 1000 ? '' : formatWaitingTimeWithMillsecond(ticks);
    this.timer = setTimeout(() => {
      if (!this.timestamp) {
        this.timer = null;
        return;
      }
      this.updateUnresponsiveTime();
    }, 1000);
  }

  render() {
    return (
      <div className={styles.chatItemPrompt}>
        {this.props.chatItem.messageNumber > 0 && (
          <PromptNumber
            customClass={styles.promptNumber}
            number={this.props.chatItem.messageNumber}
          />
        )}
        <span className={styles.idleStyle} ref={(span) => { this.timestamp = span; }} />
        {this.props.chatItem.isShowDeleteIcon && (
          <Icon
            type={icons.close}
            size={10}
            className={styles.deleteIcon}
            onClick={(e) => {
              e.stopPropagation();
              this.props.onDeleteItem(this.props.chatItem);
            }}
          />
        )
        }
      </div>
    );
  }
}
ChatItemPrompt.propTypes = {
  chatItem: PropTypes.shape({
    isShowDeleteIcon: PropTypes.bool.isRequired,
    messageNumber: PropTypes.number.isRequired,
    unresponseBeginTime: PropTypes.number,
    currentStatus: PropTypes.number.isRequired,
  }),
  isShowUnreposiveTime: PropTypes.bool.isRequired,
  onDeleteItem: PropTypes.func.isRequired,
};
ChatItemPrompt.examples = {
  'Default Example': {
    description: 'chat prompt',
    component:
      <ChatItemPrompt
        messageNumber={5}
        isShowChatItemsViaGroup={1000}
        isShowUnreposiveTime
        isShowDeleteIcon
      />
        ,
  },
};
export default ChatItemPrompt;



