import React from 'react';
import classnames from 'classnames';
import ReactTooltip from 'react-tooltip';
import { Facebook, googlePlus, Zendesk, Salesforce } from '../../constants/enums/socialType';
import Icon from './Icon';
import style from './Icon.css';
import Link from '../Link/Link';


class Social extends React.Component {
  componentDidMount() {
    // force tooltip re-render to be after children mount
    if (this.timer) {
      clearTimeout(this.timer);
    }
    this.timer = setTimeout(() => ReactTooltip.rebuild(), 0);
  }
  shouldComponentUpdate(nextProps) {
    return nextProps.type !== this.props.type &&
           nextProps.socialUrl !== this.props.socialUrl;
  }
  componentDidUpdate() {
    if (this.timer) {
      clearTimeout(this.timer);
    }
    this.timer = setTimeout(() => ReactTooltip.rebuild(), 0);
  }

  componentWillUnmount() {
    if (this.timer !== null) {
      clearTimeout(this.timer);
      this.timer = null;
    }
  }
  render() {
    let tooltip = '';
    let classname = '';
    switch (this.props.type) {
      case Facebook: {
        classname = 'facebook';
        tooltip = 'Facebook';
        break;
      }
      case googlePlus: {
        classname = 'googlePlus';
        tooltip = 'GooglePlus';
        break;
      }
      case Zendesk: {
        classname = 'zendesk';
        tooltip = 'Zendesk';
        break;
      }
      case Salesforce: {
        classname = 'salesforce';
        tooltip = 'Salesforce';
        break;
      }
      default: break;
    }
    return (
      <span data-tip={tooltip} data-for="chatContainer">
        <Link newWindow href={this.props.socialUrl}>
          <Icon className={classnames(style[`social-${classname}`], style.iconOffsetDown)} />
        </Link>
      </span>
    );
  }
}

Social.propTypes = {
  type: React.PropTypes.number,
  socialUrl: React.PropTypes.string,
};

export default Social;



