import React from 'react';
import classnames from 'classnames';
import Button from 'src/components/Button/Button';
import chatActionStyle from '../../ChatSection/ChatAction/ChatAction.css';
import Icon from '../../Icon/Icon';
import { icons as enums } from '../../../constants/enums';
import ModalSendTranscript from '../../Modal/SendTranscript/SendTranscript';


class SendEmail extends React.Component {
  constructor() {
    super();
    this.state = {
      open: false,
    };
    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
  }
  closeModal() {
    this.setState(
      { open: false }
    );
  }
  openModal() {
    const props = this.props;
    props.initialSendTranscriptForm(props.visitorId, props.chatId, props.transcriptInfo);
    this.setState(
      { open: true }
    );
  }
  render() {
    return (
      <div>
        <Button
          type="noborder"
          className={classnames(
            chatActionStyle.chatActionIcon,
            !this.props.ifEnable && chatActionStyle.chatActionIconDisabled
          )}
          onClick={this.props.ifEnable ? this.openModal : undefined}
          data-tip="Send transcript to a specified email account."
          data-for="chatContainer"
        >
          <Icon type={enums.sendEmail} />
        </Button>
        {this.state.open && <ModalSendTranscript
          closeModal={this.closeModal}
          handleSendTranscript={this.props.handleSendTranscript}
          transcriptInfo={this.props.transcriptInfo}
          responseErrorMessage={this.props.responseErrorMessage}
          sendEmailSuccessMessage={this.props.sendEmailSuccessMessage}
          showResponseErrorMessage={this.props.showResponseErrorMessage}
          hideResponseErrorMessage={this.props.hideResponseErrorMessage}
          hideSendEmailSuccessMessage={this.props.hideSendEmailSuccessMessage}
        />}
      </div>
    );
  }
}

export default SendEmail;

SendEmail.propTypes = {
  ifEnable: React.PropTypes.bool,
  handleSendTranscript: React.PropTypes.func.isRequired,
  transcriptInfo: React.PropTypes.shape({ }),
  initialSendTranscriptForm: React.PropTypes.func,
  responseErrorMessage: React.PropTypes.string,
  sendEmailSuccessMessage: React.PropTypes.string,
  showResponseErrorMessage: React.PropTypes.func,
  hideResponseErrorMessage: React.PropTypes.func,
  hideSendEmailSuccessMessage: React.PropTypes.func,
};



