import React from 'react';
// import classnames from 'classnames';

// import ReactModal from 'react-modal';
import Modal from '../Modal';
import ModalStyle from '../Modal.css';
import RadioButton from '../../RadioButton/RadioButton';
import Button from '../../Button/Button';

import SelectDepartment from './SelectDepartment';
import SelectAgentByDepartmentId from './SelectAgentByDepartmentId';
import Checkbox from '../../Checkbox/Checkbox';

class ModalTransfer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      transferTo: props.ifDepartmentEnabled ? '0' : '1',
      departmentId: 0,
      selectedDepartmentIndex: -1,
      agentId: 0,
      selectedAgentIndex: -1,
      enableCloseChat: false,
    };
    this.handleChangeTransferTo = this.handleChangeTransferTo.bind(this);
    this.handleSelectDepartment = this.handleSelectDepartment.bind(this);
    this.handleSelectAgent = this.handleSelectAgent.bind(this);
    this.switchCloseChatFlag = this.switchCloseChatFlag.bind(this);
  }
  handleChangeTransferTo(event) {
    this.setState({
      transferTo: event.target.value,
    });
  }
  handleSelectDepartment(departmentId, index) {
    this.setState({
      departmentId,
      selectedDepartmentIndex: index,
    });
  }
  handleSelectAgent(agentId, index) {
    this.setState({
      agentId,
      selectedAgentIndex: index,
    });
  }
  switchCloseChatFlag(tag) {
    this.setState({ enableCloseChat: tag });
  }
  render() {
    const {
      closeModal,
      ifDepartmentEnabled,
      onlineDepartments,
      onlineDepartmentOptions,
      allAvailableAgents,
    } = this.props;
    return (
      <Modal
        closeModal={closeModal}
        className={ModalStyle.modalTransfer}
        title={ifDepartmentEnabled ? 'Transfer the Chat to' : 'Choose an Agent to Transfer'}
        actions={{
          okProps: {
            text: 'Transfer',
            eventClick: () => {
              this.props.closeModal();
              this.props.onTransferChat(
                this.state.transferTo,
                this.props.visitorId,
                this.state.departmentId,
                this.state.agentId,
                this.state.enableCloseChat,
              );
            },
          },
          cancelProps: { text: 'Cancel' },
        }}
      >
        {
          ifDepartmentEnabled ?
            <RadioButton
              name="transfer"
              radioOption={
                [
                  {
                    text: 'Department',
                    value: '0',
                    checked: this.state.transferTo === '0',
                  },
                  {
                    text: 'Agent',
                    value: '1',
                    checked: this.state.transferTo === '1',
                  },
                ]
              }
              className={ModalStyle.transferChoose}
              handleChange={this.handleChangeTransferTo}
            />
            :
            ''
        }
        {
          this.state.transferTo === '0' ?
            <SelectDepartment
              onlineDepartments={onlineDepartments}
              handleSelectDepartment={this.handleSelectDepartment}
              selectedDepartmentIndex={this.state.selectedDepartmentIndex}
            />
            :
            <SelectAgentByDepartmentId
              ifDepartmentEnabled={ifDepartmentEnabled}
              allAvailableAgents={allAvailableAgents}
              onlineDepartments={onlineDepartments}
              onlineDepartmentOptions={onlineDepartmentOptions}
              handleSelectAgent={this.handleSelectAgent}
              selectedAgentIndex={this.state.selectedAgentIndex}
            />
        }
        <Checkbox
          input={{ value: this.state.enableCloseChat, onChange: this.switchCloseChatFlag }}
          text="Leave chat after transfer"
          className={ModalStyle.leavechataftertransfer}
        />
      </Modal>
    );
  }
}

export default ModalTransfer;

ModalTransfer.propTypes = {
  closeModal: React.PropTypes.func.isRequired,
  visitorId: React.PropTypes.number,
  ifDepartmentEnabled: React.PropTypes.bool.isRequired,
  onlineDepartments: React.PropTypes.array,
  allAvailableAgents: React.PropTypes.array,
  onlineDepartmentOptions: React.PropTypes.array,
  onTransferChat: React.PropTypes.func,
};



