import React from 'react';
import classnames from 'classnames';
import Button from 'src/components/Button/Button';
import chatActionStyle from '../../ChatSection/ChatAction/ChatAction.css';
import Icon from '../../Icon/Icon';
import { icons as enums } from '../../../constants/enums';

import PromptGotoMeeting from '../../Prompt/PromptGotoMeeting/PromptGotoMeeting';

class GotoMeeting extends React.Component {
  constructor() {
    super();
    this.state = {
      isOpenGotoMeeting: false,
    };
    this.toggleOpenPrompt = this.toggleOpenPrompt.bind(this);
    this.closeOpenPrompt = this.closeOpenPrompt.bind(this);
    this.handleClick = this.handleClick.bind(this);
  }
  componentDidMount() {
    document.addEventListener('click', this.handleClick, false);
  }
  componentWillUnmount() {
    document.removeEventListener('click', this.handleClick, false);
  }
  handleClick(event) {
    const container = this.container;
    if (container && !container.contains(event.target) && this.state.isOpenGotoMeeting) {
      this.setState({ isOpenGotoMeeting: false });
    }
  }
  toggleOpenPrompt() {
    this.setState({
      isOpenGotoMeeting: !this.state.isOpenGotoMeeting,
    });
  }
  closeOpenPrompt() {
    this.setState({
      isOpenGotoMeeting: false,
    });
  }
  render() {
    return (
      <div ref={(element) => { this.container = element; }}>
        <Button
          type="noborder"
          className={
            classnames(
              chatActionStyle.chatActionIcon,
              !this.props.ifEnable && chatActionStyle.chatActionIconDisabled,
              this.state.isOpen && chatActionStyle.iconActive
            )
          }
          onClick={this.props.ifEnable ? this.toggleOpenPrompt : () => {}}
          data-tip="Start screen sharing session with your visitors through GoToMeeting."
          data-for="chatContainer"
        >
          <Icon type={enums.gotomeeting} />
        </Button>
        {
          this.state.isOpenGotoMeeting &&
            <PromptGotoMeeting
              closePrompt={this.closeOpenPrompt}
              visitorId={this.props.visitorId}
              onRequestGotoMeeting={this.props.onRequestGotoMeeting}
            />
        }
      </div>
    );
  }
}

export default GotoMeeting;

GotoMeeting.propTypes = {
  ifEnable: React.PropTypes.bool,
  ifDisplay: React.PropTypes.bool,
  visitorId: React.PropTypes.number.isRequired,
  onRequestGotoMeeting: React.PropTypes.func.isRequired,
};



