import * as helpers from 'src/utils/common';
import * as _ from 'lodash';
import memoize from 'lru-memoize';
import { chatStatus, chatListGroup } from '../../constants/enums';
import * as selectors from '../../reducers/selectors';
import * as permissionType from '../../constants/enums/liveChatPermission';
import memoryStore from '../../services/memoryStore';
import * as memoryStoreKey from '../../constants/memoryStore';

function getOpenedStatusById(openedStatus = {}, id) {
  if (_.isUndefined(openedStatus[id])) return true;
  return openedStatus[id];
}
/* refresh status */
function getStatus(visitor, agentId) {
  const status = visitor.get('status');
  if (status === chatStatus.waitingForChat) {
    return visitor.get('autoInvitationId') > 0 ?
      chatStatus.invitedAndWaitingForChat :
      chatStatus.waitingForChat;
  } else if (status === chatStatus.transferring) {
    return chatStatus.transferring;
  } else if (status === chatStatus.chatting) {
    return helpers.isAgentInMonitorList(visitor, agentId) ?
      chatStatus.monitorChatting :
      chatStatus.chatting;
  } else if (status === chatStatus.voiceChatting) {
    return chatStatus.voiceChatting;
  } else if (status === chatStatus.chatEnded) {
    return helpers.isAgentInMonitorList(visitor, agentId) ?
      chatStatus.monitorChatEnded :
      chatStatus.chatEnded;
  } else if (helpers.checkIfWaitingVisitorAcceptInvitationFromMe(visitor, agentId)) {
    return chatStatus.manuallyInvitedAndWatingVisitor;
  } else if (helpers.checkIfVisitorRefuseMyInvitation(visitor, agentId)) {
    return chatStatus.refusedByVisitor;
  }
  return chatStatus.chatEnded;
}

function getGroupType(visitor, agentId, isGroup) {
  if (!isGroup) {
    return chatListGroup.noGroup;
  }
  const status = visitor.get('status');
  if (status === chatStatus.chatting ||
    status === chatStatus.voiceChatting ||
    (status === chatStatus.transferring &&
      helpers.isAgentInChatActiveList(visitor, agentId)
    )) {
    return chatListGroup.onGoing;
  } else if (status === chatStatus.waitingForChat ||
    status === chatStatus.transferring) {
    return chatListGroup.inComing;
  } else if (helpers.isAgentInChatActiveList(visitor, agentId) &&
    (status === chatStatus.manuallyInvitedAndWatingVisitor ||
    status === chatStatus.refusedByVisitor)
  ) {
    return chatListGroup.invited;
  }
  return chatListGroup.chatEnded;
}

function getUnresponseBeginTime(visitor, chat, delay, isOpened) {
  if (!isOpened) return null;
  let beginTime = null;
  let realDelay = delay;
  switch (visitor.get('status')) {
    case chatStatus.waitingForChat:
      beginTime = visitor.get('chatRequestTime');
      break;
    case chatStatus.transferring:
      beginTime = visitor.get('transferTime');
      break;
    default:
      realDelay = 0;
      beginTime = chat.get('lastRepliedTime');
      break;
  }
  if (!(beginTime instanceof Date)) return null;

  return beginTime.getTime() + (realDelay * 1000);
}

export const getSegmentations = (segments, ids) => {
  if (!Array.isArray(ids)) {
    return [];
  }
  return segments
    .filter(seg => ids.indexOf(seg.id) >= 0)
    .map(seg => ({ name: seg.name, color: `#${seg.color.toString(16)}` }));
};

export function mapToComponentProps(
  visitor, chat, selectedChatId, agents, agentId, departments, segments, delay
) {
  const isAgentInChatActiveList = helpers.isAgentInChatActiveList(visitor, agentId);
  const isAgentInMonitorList = helpers.isAgentInMonitorList(visitor, agentId);
  const isAgentInInvolvedList = helpers.isAgentInInvolvedList(visitor, agentId);
  const status = visitor.get('status');
  const chatId = chat.get('id');
  const isWaiting = helpers.isWaiting(visitor, agentId, departments);
  const isVisitorChatting = helpers.isChatting(visitor);
  const isMonitoring = isVisitorChatting && isAgentInMonitorList;
  const isChatting = isVisitorChatting && isAgentInChatActiveList;
  const isShowClose = helpers.isChatEnded(visitor) ||
    (status === chatStatus.chatEnded && isAgentInMonitorList) ||
    (isAgentInInvolvedList &&
    !(isVisitorChatting &&
    isAgentInChatActiveList) &&
    !isMonitoring &&
    status !== chatStatus.transferring &&
    status !== chatStatus.waitingForChat);

  const departmentId = visitor.get('departmentId');
  const department = _.find(departments, dep => departmentId === dep.id);
  const showUnreadMessages = isChatting || isMonitoring || status === chatStatus.chatEnded;

  return {
    status,
    waitingChatOrChatBegins: visitor.get('waitingChatOrChatBegins'),
    visitorId: visitor.get('id'),
    chatId,
    visitor: {
      name: visitor.get('latestName'),
      agentName: helpers.getAgentNames(visitor, agentId, agents),
      department: department ? department.name : null,
    },
    segments: getSegmentations(segments, visitor.get('segmentIds')),
    isPinned: chat.get('isPinned'),
    isShowWrapUp: !isWaiting &&
                  chat.get('ifShowWrapupButton') &&
                  !chat.get('wrapupSubmitted') &&
                  chatId !== selectedChatId,
    isShowAcceptAndRefuse: isWaiting,
    isShowDeleteIcon: isShowClose,
    unresponseBeginTime: getUnresponseBeginTime(visitor, chat, delay, (isWaiting || isChatting)),
    currentStatus: getStatus(visitor, agentId),
    messageNumber: showUnreadMessages ? chat.get('unreadMessages') : 0,
    isSelected: (chatId === selectedChatId),
  };
}

function getDefaultStructure(opendStatus) {
  return {
    chats: [],
    groupItems: [
      {
        id: 'ongoingChats',
        description: 'Ongoing Chats',
        opened: getOpenedStatusById(opendStatus, 'ongoingChats'),
        chats: [],
      },
      {
        id: 'incomingChats',
        description: 'Incoming Chats',
        opened: getOpenedStatusById(opendStatus, 'incomingChats'),
        chats: [],
      },
      {
        id: 'manuallyInvited',
        description: 'Manually Invited',
        opened: getOpenedStatusById(opendStatus, 'manuallyInvited'),
        chats: [],
      },
      {
        id: 'endedChats',
        description: 'Ended Chats',
        opened: getOpenedStatusById(opendStatus, 'endedChats'),
        chats: [],
      },
    ],
    ifDepartmentEnabled: true,
    isShowChatItemsViaStatus: false,
    isShowUnreposiveTime: true,
    isShowDepartment: true,
    ifEnableSegmentation: true,
    enableRefuseChat: true,
  };
}

function initData(chats, visitors, selectedChatId, siteInfo, preferenceOther,
  enableRefuseChat, delay, agentId, agents, departments, segments, openedStatus
) {
  const chatList = getDefaultStructure(openedStatus);
  /* init global setting */
  chatList.ifDepartmentEnabled = siteInfo.ifDepartmentEnabled;
  chatList.isShowChatItemsViaStatus = preferenceOther.ifGroupChats;
  chatList.isShowUnreposiveTime = preferenceOther.ifShowUnresponseTime;
  chatList.isShowDepartment = preferenceOther.ifShowDepartment;
  chatList.ifEnableSegmentation = siteInfo.ifEnableSegmentation;
  chatList.enableRefuseChat = enableRefuseChat;

  return chats.reduce((lists, chat, key) => {
    const visitor = chat.get('isUnfinishedWrapup') ?
      visitors.get(`v${chat.get('visitorId')}c${key}`) : visitors.get(chat.get('visitorId'));
    if (!visitor) return lists;
    const groupType = getGroupType(visitor, agentId, lists.isShowChatItemsViaStatus);
    const item = mapToComponentProps(
      visitor, chat, selectedChatId, agents, agentId,
      departments, segments, delay
    );
    switch (groupType) {
      case chatListGroup.onGoing:
        lists.groupItems[0].chats.splice(0, 0, item);
        break;
      case chatListGroup.inComing:
        lists.groupItems[1].chats.push(item);
        break;
      case chatListGroup.invited:
        lists.groupItems[2].chats.splice(0, 0, item);
        break;
      case chatListGroup.chatEnded:
        lists.groupItems[3].chats.splice(0, 0, item);
        break;
      default:
        lists.chats.splice(0, 0, item);
        break;
    }
    return lists;
  }, chatList);
}

const memoizedInitData = memoize(1)(initData);

export function createChatListState(state) {
  const siteInfo = selectors.getSiteInfo(state);
  const preferenceOther = selectors.getPreferenceOthers(state);
  const enableRefuseChat = selectors.getPermission(state, permissionType.refuseChat);
  const delay = selectors.getTimeDelay(state) || 0;
  const agentId = selectors.getCurrentAgentId(state);
  const chats = selectors.getChats(state);
  const selectedChatId = selectors.getSelectedChatId(state);
  const agents = selectors.getAgents(state);
  const departments = selectors.getDepartments(state);
  const segments = selectors.getSegmentations(state);
  const visitors = selectors.getVisitors(state);
  const openedGroupStatus = memoryStore.get(memoryStoreKey.openedGroupStatus, null, {});
  return memoizedInitData(chats, visitors, selectedChatId, siteInfo, preferenceOther,
    enableRefuseChat, delay, agentId, agents, departments, segments, openedGroupStatus);
}



