import React, { PropTypes } from 'react';
import _ from 'lodash';
import classnames from 'classnames';
import styles from './ChatItem.css';
import Icon from '../Icon/Icon';
import { chatListConfig, icons } from '../../constants/enums';

class ChatItemConfig extends React.Component {
  shouldComponentUpdate(nextProps) {
    return !_.isEqual(this.props, nextProps);
  }
  render() {
    return (
      <div className={styles.chatItemConfig}>
        <ul className={styles.chatconfigpanel}>
          {this.props.ifDepartmentEnabled && (
            <li
              onClick={(e) => {
                e.stopPropagation();
                this.props.onClickConfig(chatListConfig.showDepartment);
              }}
            >
              <Icon
                type={icons.success}
                className={classnames(styles.configitem,
                    this.props.isShowDepartment && styles.selectedicon)}
              />
              Show department
            </li>
          )}
          <li
            onClick={(e) => {
              e.stopPropagation();
              this.props.onClickConfig(chatListConfig.showUnreposiveTime);
            }}
          >
            <Icon
              type={icons.success}
              className={classnames(styles.configitem,
                  this.props.isShowUnreposiveTime && styles.selectedicon)}
            />
            Show agent unresponsive time
          </li>
          <li
            onClick={(e) => {
              e.stopPropagation();
              this.props.onClickConfig(chatListConfig.showByStatus);
            }}
          >
            <Icon
              type={icons.success}
              className={classnames(styles.configitem,
                  this.props.isShowChatItemsViaStatus && styles.selectedicon)}
            />
            Group chat by status
          </li>
        </ul>
      </div>
    );
  }
}
ChatItemConfig.propTypes = {
  isShowUnreposiveTime: PropTypes.bool.isRequired,
  ifDepartmentEnabled: PropTypes.bool.isRequired,
  isShowDepartment: PropTypes.bool.isRequired,
  isShowChatItemsViaStatus: PropTypes.bool.isRequired,
  onClickConfig: PropTypes.func.isRequired,
};
ChatItemConfig.examples = {
  'Default Example': {
    description: 'chat config',
    component:
      <ChatItemConfig
        isShowUnreposiveTime
      />
        ,
  },
};
export default ChatItemConfig;



