import React, { PropTypes } from 'react';
import { chatStatus, icons } from '../../constants/enums';
import Icon from '../Icon/Icon';
import * as visitorStatus from '../../constants/languages/visitorStatus';

function getClassNameByCode(code) {
  switch (code) {
    case chatStatus.waitingForChat:
      return { type: icons.waitting, title: visitorStatus.waitingForChat };
    case chatStatus.voiceChatting:
      return { type: icons.voiceChatting, title: visitorStatus.voiceChatting };
    case chatStatus.chatting:
      return { type: icons.chatting, title: visitorStatus.chatting };
    case chatStatus.preChat:
      return { type: icons.prechat, title: visitorStatus.preChat };
    case chatStatus.invited:
      return { type: icons.invited, title: visitorStatus.invited };
    case chatStatus.autoInvited:
      return { type: icons.invited, title: visitorStatus.autoInvited };
    case chatStatus.offlineMessage:
      return { type: icons.offline, title: visitorStatus.offlineMessage };
    case chatStatus.refusedByAgent:
      return { type: icons.refused, title: visitorStatus.refusedByAgent };
    case chatStatus.refusedByVisitor:
      return { type: icons.refused, title: visitorStatus.refusedByVisitor };
    case chatStatus.chatEnded:
      return { type: icons.chatEnd, title: visitorStatus.chatEnded };
    case chatStatus.inSite:
      return { type: icons.insite, title: visitorStatus.inSite };
    case chatStatus.outOfSite:
      return { type: icons.outOfSite, title: visitorStatus.outOfSite };
    case chatStatus.transferring:
      return { type: icons.transferring, title: visitorStatus.transferring };
    case chatStatus.manuallyInvitedAndWatingVisitor:
      return { type: icons.inviteWaiting, title: visitorStatus.manuallyInvitedAndWaitingVisitor };
    case chatStatus.invitedAndWaitingForChat:
      return { type: icons.waitingInvite, title: visitorStatus.invitedAndWaitingForChat };
    case chatStatus.monitorChatEnded:
      return { type: icons.monitorEnded, title: visitorStatus.monitorChatEnded };
    case chatStatus.monitorChatting:
      return { type: icons.monitorChatting, title: visitorStatus.monitorChatting };
    default:
      return { type: '', color: '', title: '' };
  }
}
class ChatStatus extends React.Component {
  shouldComponentUpdate(nextProps) {
    return nextProps.statusCode !== this.props.statusCode;
  }
  render() {
    const { type, title } = getClassNameByCode(this.props.statusCode);
    return (
      <Icon
        type={type}
        size={this.props.size}
        data-tip={title}
        data-for={this.props.dataFor}
      />
    );
  }
}
ChatStatus.defaultProps = {
  size: 18,
};
ChatStatus.propTypes = {
  /* status of chat */
  statusCode: PropTypes.oneOf(Object.keys(chatStatus).map(key => chatStatus[key])).isRequired,
  /* the size of chat icon */
  size: PropTypes.oneOfType([
    PropTypes.oneOf(['larger', 'large', 'normal', 'small', 'smaller']),
    PropTypes.number,
  ]),
  /* this config for tooltip data-for */
  dataFor: PropTypes.string,
};
ChatStatus.examples = {
  'Default Example': {
    description: 'chat status',
    component:
      <ChatStatus
        statusCode={chatStatus.waitingForChat}
      />
        ,
  },
};
export default ChatStatus;



