import React from 'react';
import classnames from 'classnames';
import Link from 'src/components/Link/Link';
import chatActionStyle from '../../ChatSection/ChatAction/ChatAction.css';
import Icon from '../../Icon/Icon';
import { icons as enums } from '../../../constants/enums';

const Joinme = (props) => {
  const { joinMeIntegrationServer, siteId, visitorId, currentAgentId, agentSession } = props;
  const url = [
    `${joinMeIntegrationServer}/auth.aspx?siteId=${siteId}`,
    `visitorId=${visitorId}`,
    `operatorId=${currentAgentId}`,
    `token=${agentSession}`,
  ].join('&');
  return (
    <div>
      <Link
        type="noborder"
        className={
          classnames(
            chatActionStyle.chatActionIcon,
            !props.ifEnable && chatActionStyle.chatActionIconDisabled,
          )
        }
        href={props.ifEnable ? url : 'javascript:void(0)'}
        newWindow
        data-tip="Start screen sharing session with your visitors through Join.Me."
        data-for="chatContainer"
      >
        <Icon type={enums.joinMe} />
      </Link>
    </div>
  );
};

export default Joinme;

Joinme.propTypes = {
  ifEnable: React.PropTypes.bool,
  joinMeIntegrationServer: React.PropTypes.string,
  siteId: React.PropTypes.number.isRequired,
  currentAgentId: React.PropTypes.number.isRequired,
  visitorId: React.PropTypes.number.isRequired,
  agentSession: React.PropTypes.string.isRequired,
};



