import React from 'react';
import classnames from 'classnames';
import Button from 'src/components/Button/Button';
import chatActionStyle from '../../ChatSection/ChatAction/ChatAction.css';
import Icon from '../../Icon/Icon';
import { icons as enums } from '../../../constants/enums';
import ModalTransfer from '../../Modal/ModalTransfer/ModalTransfer';


class Transfer extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      open: false,
    };
    this.openModal = this.toggleModal.bind(this, true);
    this.closeModal = this.toggleModal.bind(this, false);
  }
  toggleModal(ifOpen) {
    this.setState({ open: ifOpen });
    if (ifOpen && this.props.onTransferIconClick) {
      this.props.onTransferIconClick();
    }
  }
  render() {
    return (
      <div>
        <Button
          type="noborder"
          onClick={this.props.ifEnable ? this.openModal : undefined}
          className={classnames(
            chatActionStyle.chatActionIcon,
            !this.props.ifEnable && chatActionStyle.chatActionIconDisabled
          )}
          data-tip="Transfer the chat to another agent or department."
          data-for="chatContainer"
        >
          <Icon type={enums.transfer} />
        </Button>
        {this.state.open && <ModalTransfer
          closeModal={this.closeModal}
          visitorId={this.props.visitorId}
          ifDepartmentEnabled={this.props.transfer.ifDepartmentEnabled}
          onlineDepartments={this.props.transfer.onlineDepartments}
          onlineDepartmentOptions={this.props.transfer.onlineDepartmentComponent}
          allAvailableAgents={this.props.transfer.allAvailableAgents}
          onTransferChat={this.props.onTransferChat}
        />}
      </div>
    );
  }
}

export default Transfer;

Transfer.propTypes = {
  ifEnable: React.PropTypes.bool,
  visitorId: React.PropTypes.number,
  transfer: React.PropTypes.shape({
    ifDepartmentEnabled: React.PropTypes.bool.isRequired,
    onlineDepartments: React.PropTypes.array,
    onlineDepartmentComponent: React.PropTypes.array,
    allAvailableAgents: React.PropTypes.array,
  }).isRequired,
  onTransferChat: React.PropTypes.func,
  onTransferIconClick: React.PropTypes.func,
};



