import React from 'react';
import LazilyLoad, { importLazy } from 'src/components/LazilyLoad/LazilyLoad';
import loadingComponent from 'src/components/Icons/LoadingComponent';
import Reload from 'src/components/Widget/Reload';

export default () => (
  <LazilyLoad
    modules={{ VisitorDetailsTab: () => importLazy(new Promise((resolve, reject) => {
      require.ensure(['./VisitorDetailsTab'], (require) => {
        resolve(require('./VisitorDetailsTab'));
      }, 'details-tab').catch(reject);
    })) }}
    loading={loadingComponent}
    retry={Reload}
  >
    {
      ({ VisitorDetailsTab }) => (
        <VisitorDetailsTab />
      )
    }
  </LazilyLoad>
);



