import React, { PropTypes } from 'react';
import classnames from 'classnames';
import _ from 'lodash';
import Tooltip from 'src/components/Tooltip/Tooltip';
import styles from './ChatItem.css';
import ChatStatus from '../ChatStatus/ChatStatus';
import { chatStatus, icons } from '../../constants/enums';
import Icon from '../Icon/Icon';
import ChatItemDetail from './ChatItemDetail';
import ChatItemPrompt from './ChatItemPrompt';
import ChatItemSegment from './ChatItemSegment';

class ChatItem extends React.Component {
  componentDidMount() {
    Tooltip.rebuild();
  }

  shouldComponentUpdate(nextProps) {
    return !_.isEqual(this.props, nextProps);
  }

  componentDidUpdate() {
    Tooltip.rebuild();
  }

  render() {
    const tooltipId = 'chatlist';
    return (
      <div
        className={classnames(styles.chatitemcontainer,
          this.props.chatItem.isShowAcceptAndRefuse && styles.acceptStyle,
          this.props.chatItem.isSelected && styles.selected,
          (_.isEmpty(this.props.chatItem.visitor.agentName) &&
          !this.props.chatItem.isShowAcceptAndRefuse &&
          !this.props.chatItem.isShowWrapUp &&
          (!this.props.isShowDepartment ||
           (this.props.isShowDepartment &&
          _.isEmpty(this.props.chatItem.visitor.department))))
          && styles.nobutton)}
        onClick={() => this.props.onItemClick(this.props.chatItem)}
      >
        <div
          className={styles.captureitem}
        >
          <Icon
            type={icons.pin}
            size={14}
            data-tip={this.props.chatItem.isPinned ? 'Unpin' : 'Pin'}
            data-for={tooltipId}
            className={classnames(styles.pinnStyle,
              this.props.chatItem.isPinned ? styles.pinned : styles.nopinned)}
            onClick={() => {
              this.props.onClickPin(this.props.chatItem);
            }}
          />
        </div>
        <div className={styles.statusitem}>
          <ChatStatus
            statusCode={this.props.chatItem.currentStatus}
            dataFor={tooltipId}
          />
        </div>
        <div className={styles.detailitem}>
          <ChatItemDetail
            chatItem={this.props.chatItem}
            enableRefuseChat={this.props.enableRefuseChat}
            isShowDepartment={this.props.isShowDepartment}
            onAcceptChat={this.props.onAcceptChat}
            onRefusedChat={this.props.onRefusedChat}
            onWrapUp={this.props.onWrapUp}
          />
        </div>
        <div className={styles.infoitem}>
          <ChatItemPrompt
            chatItem={this.props.chatItem}
            isShowUnreposiveTime={this.props.isShowUnreposiveTime}
            onDeleteItem={this.props.onDeleteItem}
          />
        </div>
        <div className={styles.segmentitem}>
          <ChatItemSegment
            chatItem={this.props.chatItem}
            ifEnableSegmentation={this.props.ifEnableSegmentation}
            dataFor={tooltipId}
          />
        </div>
      </div>
    );
  }
}

ChatItem.propTypes = {
  chatItem: PropTypes.shape({
    visitorId: PropTypes.number.isRequired,
    visitor: PropTypes.shape({
      name: PropTypes.string,
      agentName: PropTypes.string,
      department: PropTypes.string,
    }).isRequired,
    segments: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string,
      color: PropTypes.string,
    })).isRequired,
    isPinned: PropTypes.bool.isRequired,
    isShowWrapUp: PropTypes.bool.isRequired,
    isShowAcceptAndRefuse: PropTypes.bool.isRequired,
    isShowDeleteIcon: PropTypes.bool.isRequired,
    unresponseBeginTime: PropTypes.number,
    currentStatus: PropTypes.number.isRequired,
    messageNumber: PropTypes.number.isRequired,
    isSelected: PropTypes.bool.isRequired,
  }),
  /* following setting for all chat */
  enableRefuseChat: PropTypes.bool.isRequired,
  ifEnableSegmentation: PropTypes.bool.isRequired,
  isShowUnreposiveTime: PropTypes.bool.isRequired,
  isShowDepartment: PropTypes.bool.isRequired,
  onClickPin: PropTypes.func.isRequired,
  onItemClick: PropTypes.func.isRequired,
  onAcceptChat: PropTypes.func.isRequired,
  onRefusedChat: PropTypes.func.isRequired,
  onWrapUp: PropTypes.func.isRequired,
  onDeleteItem: PropTypes.func.isRequired,
};
ChatItem.examples = {
  'Default Example': {
    description: 'chat item',
    component:
      <ChatItem
        currentStatus={chatStatus.waitingForChat}
        visitor={{
          name: 'Andy',
          ip: '0.0.0.0',
          department: 'department',
        }}
        segments={[
          { name: 'name1', color: '#ccc' },
        ]}
      />
        ,
  },
};
export default ChatItem;



