import React from 'react';

import PromptStyle from '../Prompt.css';
import Button from '../../Button/Button';
import Icon from '../../Icon/Icon';
import { icons } from '../../../constants/enums';

const PromptGotoMeeting = (props) => (
  <div className={PromptStyle.promptWindow}>
    <button
      className={PromptStyle.close}
      onClick={props.closePrompt}
    >
      <Icon type={icons.close} size={10} />
    </button>
    <p>
      Would you like to request a screen sharing session with the visitor?
    </p>
    <div className={PromptStyle.action}>
      <Button
        type="primary"
        text="Yes"
        onClick={
            () => {
              props.onRequestGotoMeeting(props.visitorId);
              props.closePrompt();
            }
        }
      />
      <Button type="default" text="No" onClick={props.closePrompt} />
    </div>
  </div>
);

export default PromptGotoMeeting;

PromptGotoMeeting.propTypes = {
  // ifOpen: React.PropTypes.bool.isRequired,
  closePrompt: React.PropTypes.func.isRequired,
  visitorId: React.PropTypes.number.isRequired,
  onRequestGotoMeeting: React.PropTypes.func.isRequired,
};



