import React from 'react';
import style from './FileUploadProgress.css';

const FileUploadProgress = props => (
  <div className={style.uploadProgressContainer}>
    {
      (props.fileUploadProgressNumber > 0 && props.fileUploadProgressNumber < 100) ?
        <div className={style.uploadProgress}>
          <span className={style.uploadFileName}>
            {props.fileName}
          </span>
          <span className={style.uploadProgressPercent}>{props.fileUploadProgressNumber}%</span>
          <button
            className={style.cancelUpload}
            onClick={() => props.handleAbortUpload(props.file, props.fileName)}
          >
          &times;
          </button>
        </div>
        : ''
    }
  </div>
);

export default FileUploadProgress;

FileUploadProgress.propTypes = {
  file: React.PropTypes.object,
  fileName: React.PropTypes.string.isRequired,
  fileUploadProgressNumber: React.PropTypes.number.isRequired,
  handleAbortUpload: React.PropTypes.func.isRequired,
};



