import React from 'react';
import classnames from 'classnames';
import Icon from './Icon';
import style from './Icon.css';


const Browser = (props) => {
  const tooltip = props.name !== '-' ? props.name : 'Unknown';
  let classname = '';
  const nameLowerCase = props.name.toLowerCase();
  if (nameLowerCase.indexOf('chrome') !== -1) {
    classname = 'chrome';
  } else if (nameLowerCase.indexOf('ie') !== -1) {
    classname = 'ie';
  } else if (nameLowerCase.indexOf('edge') !== -1) {
    classname = 'edge';
  } else if (nameLowerCase.indexOf('firefox') !== -1) {
    classname = 'firefox';
  } else if (nameLowerCase.indexOf('safari') !== -1) {
    classname = 'safari';
  } else if (nameLowerCase.indexOf('opera') !== -1) {
    classname = 'opera';
  } else {
    classname = 'default';
  }
  return (
    <span data-tip={tooltip} data-for="chatContainer">
      <Icon className={classnames(style[`browser-${classname}`], style.iconOffsetDown)} />
    </span>
  );
};

export default Browser;

Browser.propTypes = {
  name: React.PropTypes.string,
};



