import React from 'react';
// import { reduxForm, Field } from 'redux-form';
import * as _ from 'lodash';
import { stateToHTML } from 'draft-js-export-html';
import LazilyLoad, { importLazy } from 'src/components/LazilyLoad/LazilyLoad';
import loadingComponent from 'src/components/Icons/LoadingComponent';

import Modal from '../Modal';
import ModalStyle from '../Modal.css';
// import CommonStyle from '../../CommonResources/common.css';
import Input from '../../Input/Input';
// import Button from '../../Button/Button';
// import RadioButton from '../../RadioButton/RadioButton';
import memoryStore from '../../../services/memoryStore';
import { memoryKey } from './SendTranscriptBody';

class SendTranscript extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      to: props.transcriptInfo.to,
      subject: props.transcriptInfo.subject,
      body: props.transcriptInfo.body.content,
      isSending: false,
    };
    this.hideResponseErrorMessage = this.hideResponseErrorMessage.bind(this);
    this.hideSendEmailSuccessMessage = this.hideSendEmailSuccessMessage.bind(this);
    this.onToChange = this.onToChange.bind(this);
    this.onSubjectChange = this.onSubjectChange.bind(this);
    this.onBodyChange = this.onBodyChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.responseErrorMessage) {
      this.setState({
        isSending: false,
      });
      if (this.timeoutId !== undefined) {
        clearTimeout(this.timeoutId);
      }
      this.timeoutId = setTimeout(this.hideResponseErrorMessage, 5000);
    }
    if (nextProps.sendEmailSuccessMessage) {
      if (this.successTimeoutId !== undefined) {
        clearTimeout(this.successTimeoutId);
      }
      this.successTimeoutId = setTimeout(() => {
        this.hideSendEmailSuccessMessage();
        this.setState({
          isSending: false,
        });
      }, 5000);
    }
    if (!_.isEqual(this.props.transcriptInfo.body.content,
          nextProps.transcriptInfo.body.content)) {
      this.setState({
        body: nextProps.transcriptInfo.body.content,
      });
    }
  }
  onToChange(e) {
    this.setState({
      to: e.target.value,
    });
  }
  onSubjectChange(e) {
    this.setState({
      subject: e.target.value,
    });
  }
  onBodyChange(body) {
    this.setState({
      body,
    });
  }

  onSubmit() {
    if (!this.state.to) {
      this.props.showResponseErrorMessage('Email to is required');
      return;
    } else if (this.state.to.indexOf('@') < 0) {
      this.props.showResponseErrorMessage('Email to is invalid');
      return;
    }
    this.setState({
      isSending: true,
    });
    const bodyState = memoryStore.get(`${memoryKey}_${this.props.transcriptInfo.chatId}`, null, null);
    const bodyHtml = stateToHTML(bodyState.editorState.getCurrentContent());

    const bodyReplaceStartP = _.replace(bodyHtml, new RegExp('<p>', 'g'), '<tr><td>');
    const bodyReplaceEndP = _.replace(bodyReplaceStartP, new RegExp('</p>', 'g'), '</td></tr>');
    const bodyAddColor = _.replace(bodyReplaceEndP, new RegExp('<td><strong>', 'g'), '<td style="color:#329fd9"><strong>');
    const form = {
      to: this.state.to,
      subject: this.state.subject,
      body: `<table cellspacing="0" cellpadding="0"><tr><td>${bodyAddColor}</td></tr></table>`,
      attachment: this.props.transcriptInfo.body.attachments,
    };
    this.props.handleSendTranscript(this.props.transcriptInfo.chatId, form);
  }
  hideResponseErrorMessage() {
    this.props.hideResponseErrorMessage();
  }
  hideSendEmailSuccessMessage() {
    this.props.hideSendEmailSuccessMessage();
  }
  render() {
    return (
      <Modal
        closeModal={this.props.closeModal}
        className={ModalStyle.modalSendTranscript}
        title="Send Transcript"
        actions={{
          okProps: { text: 'OK', disabled: this.state.isSending, eventClick: this.onSubmit },
          cancelProps: { text: 'Cancel' },
        }}
      >
        <div className={ModalStyle.formTranscriptLine}>
          <span>To:</span>
          <Input
            className={ModalStyle.input}
            type="text"
            meta={{}}
            input={{
              value: this.state.to,
              onChange: this.onToChange,
            }}
          />
        </div>
        <div className={ModalStyle.formTranscriptLine}>
          <span>Subject:</span>
          <Input
            className={ModalStyle.input}
            type="text"
            meta={{}}
            input={{
              value: this.state.subject,
              onChange: this.onSubjectChange,
            }}
          />
        </div>

        <div className={ModalStyle.formTranscriptLine}>
          <span>Body:</span>
          <LazilyLoad
            modules={{ SendTranscriptBody: () => importLazy(new Promise((resolve, reject) => {
              require.ensure(['./SendTranscriptBody'], (require) => {
                resolve(require('./SendTranscriptBody'));
              }, 'editor').catch(reject);
            })) }}
            loading={loadingComponent}
          >
            {
              ({ SendTranscriptBody }) => (
                <SendTranscriptBody
                  transcriptBodyAdditionInfo={this.props.transcriptInfo}
                  input={{
                    value: this.state.body,
                    onChange: (body) => { this.onBodyChange(body); },
                  }}
                />
              )
            }
          </LazilyLoad>
        </div>
        <div className={ModalStyle.formTranscriptLine}>
          <span>Attachment:</span>
          <div className={ModalStyle.attachmentsContainer}>
            {this.props.transcriptInfo.body.attachments}
          </div>
        </div>
        {
          this.props.responseErrorMessage ?
            <div className={ModalStyle.errorContainer}>
              <span className={ModalStyle.message}>{this.props.responseErrorMessage}</span>
            </div>
            :
            ''
        }
        {
          this.props.sendEmailSuccessMessage ?
            <div className={ModalStyle.successContainer}>
              <span className={ModalStyle.message}>{this.props.sendEmailSuccessMessage}</span>
            </div>
            :
            ''
        }
      </Modal>
    );
  }

}
export default SendTranscript;

SendTranscript.propTypes = {
  // ifOpen: React.PropTypes.bool.isRequired,
  closeModal: React.PropTypes.func.isRequired,
  handleSubmit: React.PropTypes.func,
  transcriptInfo: React.PropTypes.shape({
    subject: React.PropTypes.string,
    to: React.PropTypes.string,
    body: React.PropTypes.shape({
      attachments: React.PropTypes.string,
      content: React.PropTypes.string,
    }),
    chatId: React.PropTypes.number.isRequired,
  }),
  responseErrorMessage: React.PropTypes.string,
  sendEmailSuccessMessage: React.PropTypes.string,
  showResponseErrorMessage: React.PropTypes.func,
  hideResponseErrorMessage: React.PropTypes.func,
  hideSendEmailSuccessMessage: React.PropTypes.func,
  handleSendTranscript: React.PropTypes.func,
};





