import React from 'react';
import commonStyle from '../../CommonResources/common.css';
import ChatActionStyle from './ChatAction.css';
import Font from '../../Icons/ChatAction/Font';
import Transfer from '../../Icons/ChatAction/Transfer';
import SendFile from '../../Icons/ChatAction/SendFile';
import SendEmail from '../../Icons/ChatAction/SendEmail';
import AttachTicket from '../../Icons/ChatAction/AttachTicket';
import Translate from '../../Icons/ChatAction/Translate';
import GoToMeeting from '../../Icons/ChatAction/GoToMeeting';
import Joinme from '../../Icons/ChatAction/Joinme';
import SecureForm from '../../Icons/ChatAction/SecureForm';
import { shouldComponentUpdateGen } from '../../../utils/helper';

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'ChatAction',
  [
    // 'siteId', // site id should never be changed
    'selectedFontSize',
    'visitorId',
    'chatId',
    'ticketId',
    'chatActionDisplay',
    'chatActionEnable',
    'transcriptInfo',
    'responseErrorMessage',
    'sendEmailSuccessMessage',
    'autoTranslateInfo',
    'pciForm',
    'transfer',
    'joinMeIntegrationServer',
    'files',
  ],
);

const relative = { position: 'relative' };

class ChatAction extends React.Component {
  shouldComponentUpdate(nextProps) {
    return shouldComponentUpdateHelper(this.props, nextProps);
  }

  render() {
    const props = this.props;
    return (
      <div>
        <div className={ChatActionStyle.chatAction}>
          <ul className={commonStyle.clearfix}>
            <li>
              <Font
                selectedFontSize={props.selectedFontSize}
                onFontSelect={props.onFontSelect}
              />
            </li>
            {props.chatActionDisplay.transfer &&
              <li>
                <Transfer
                  ifEnable={props.chatActionEnable.transfer}
                  visitorId={props.visitorId}
                  transfer={props.transfer}
                  onTransferChat={props.onTransferChat}
                  onTransferIconClick={props.onTransferIconClick}
                />
              </li>
            }
            <li>
              <SendFile
                disabled={!props.chatActionEnable.sendFile}
                siteInfo={props.siteInfo}
                chatId={props.chatId}
                beforeUpload={props.beforeUpload}
                onStart={props.onSendFileStart}
                onSuccess={props.onSendFileSuccess}
                onProgress={props.onSendFileProgress}
                onError={props.onSendFileError}
                onCancel={props.onSendFileCancel}
                showErrorMessage={props.showErrorMessage}
                files={props.files}
              />
            </li>
            {props.chatActionDisplay.sendTranscript &&
              <li>
                <SendEmail
                  ifEnable={props.chatActionEnable.sendTranscript}
                  visitorId={props.visitorId}
                  chatId={props.chatId}
                  handleSendTranscript={props.handleSendTranscript}
                  transcriptInfo={props.transcriptInfo}
                  initialSendTranscriptForm={props.initialSendTranscriptForm}
                  responseErrorMessage={props.responseErrorMessage}
                  sendEmailSuccessMessage={props.sendEmailSuccessMessage}
                  showResponseErrorMessage={props.showResponseErrorMessage}
                  hideResponseErrorMessage={props.hideResponseErrorMessage}
                  hideSendEmailSuccessMessage={props.hideSendEmailSuccessMessage}
                />
              </li>
            }
            {props.chatActionDisplay.attachTicket &&
              <li>
                <AttachTicket
                  ifEnable={props.chatActionEnable.attachTicket}
                  siteId={props.siteId}
                  visitorId={props.visitorId}
                  ticketId={props.ticketId}
                  handleAttachTicket={props.handleAttachTicket}
                  gotoAttachTicket={props.gotoAttachTicket}
                  ifUseMainServer={props.ifUseMainServer}
                  responseErrorMessage={props.responseErrorMessage}
                  hideResponseErrorMessage={props.hideResponseErrorMessage}
                />
              </li>
            }
            {props.chatActionDisplay.autoTranslate &&
              <li style={relative}>
                <Translate
                  ifEnable={props.chatActionEnable.autoTranslate}
                  visitorId={props.visitorId}
                  chatId={props.chatId}
                  autoTranslateInfo={props.autoTranslateInfo}
                  onToggleAutoTranslate={props.onToggleAutoTranslate}
                  onUpdateIfPopTranslation={props.onUpdateIfPopTranslation}
                />
              </li>
            }
            {props.chatActionDisplay.gotoMeeting &&
              <li style={relative}>
                <GoToMeeting
                  ifEnable={props.chatActionEnable.gotoMeeting}
                  visitorId={props.visitorId}
                  onRequestGotoMeeting={props.onRequestGotoMeeting}
                />
              </li>
            }
            {props.chatActionDisplay.joinMe &&
              <li>
                <Joinme
                  ifEnable={props.chatActionEnable.joinMe}
                  joinMeIntegrationServer={props.joinMeIntegrationServer}
                  siteId={props.siteId}
                  visitorId={props.visitorId}
                  currentAgentId={props.currentAgentId}
                  agentSession={props.agentSession}
                />
              </li>
            }
            {props.chatActionDisplay.pciForm &&
              <li style={relative}>
                <SecureForm
                  ifDisplay={props.chatActionDisplay.pciForm}
                  ifEnable={props.chatActionEnable.pciForm}
                  visitorId={props.visitorId}
                  chatId={props.chatId}
                  pciForm={props.pciForm}
                  onRequestPCIForm={props.onRequestPCIForm}
                />
              </li>
            }
          </ul>
        </div>

      </div>
    );
  }
}
export default ChatAction;

ChatAction.propTypes = {
  siteId: React.PropTypes.number.isRequired,
  chatActionEnable: React.PropTypes.shape({
    transfer: React.PropTypes.bool,
    sendFile: React.PropTypes.bool,
    sendTranscript: React.PropTypes.bool,
    attachTicket: React.PropTypes.bool,
    autoTranslate: React.PropTypes.bool,
    gotoMeeting: React.PropTypes.bool,
    joinMe: React.PropTypes.bool,
    pciForm: React.PropTypes.bool,
  }).isRequired,
  currentAgentId: React.PropTypes.number.isRequired,
  joinMeIntegrationServer: React.PropTypes.string,
  selectedFontSize: React.PropTypes.string.isRequired,
  onFontSelect: React.PropTypes.func.isRequired,
  visitorId: React.PropTypes.number.isRequired,
  ticketId: React.PropTypes.string.isRequired,
  handleAttachTicket: React.PropTypes.func.isRequired,
  onRequestPCIForm: React.PropTypes.func.isRequired,
  onRequestGotoMeeting: React.PropTypes.func.isRequired,
  transfer: React.PropTypes.shape({ }).isRequired,
  onTransferChat: React.PropTypes.func,
  onToggleAutoTranslate: React.PropTypes.func,
  handleSendTranscript: React.PropTypes.func,
  transcriptInfo: React.PropTypes.shape({ }),
  autoTranslateInfo: React.PropTypes.shape({ }),
  chatActionDisplay: React.PropTypes.shape({
    transfer: React.PropTypes.bool,
    sendFile: React.PropTypes.bool,
    sendTranscript: React.PropTypes.bool,
    attachTicket: React.PropTypes.bool,
    autoTranslate: React.PropTypes.bool,
    gotoMeeting: React.PropTypes.bool,
    joinMe: React.PropTypes.bool,
    pciForm: React.PropTypes.bool,
  }),
  initialSendTranscriptForm: React.PropTypes.func,
  responseErrorMessage: React.PropTypes.string,
  sendEmailSuccessMessage: React.PropTypes.string,
};



