import React from 'react';
import LazilyLoad, { importLazy } from 'src/components/LazilyLoad/LazilyLoad';
import loadingComponent from 'src/components/Icons/LoadingComponent';
import Reload from 'src/components/Widget/Reload';

export default () => (
  <LazilyLoad
    modules={{ ChatTextEditor: () => importLazy(new Promise((resolve, reject) => {
      require.ensure(['./ChatTextEditor'], (require) => {
        resolve(require('./ChatTextEditor'));
      }, 'editor').catch(reject);
    })) }}
    loading={loadingComponent}
    retry={Reload}
  >
    {
      ({ ChatTextEditor }) => (
        <ChatTextEditor />
      )
    }
  </LazilyLoad>
);



