import React from 'react';
import classnames from 'classnames';
import Icon from './Icon';
import style from './Icon.css';

const OS = (props) => {
  const tooltip = props.name !== '-' ? props.name : 'Unknown';
  let classname = '';
  const nameLowerCase = props.name.toLowerCase();
  if (nameLowerCase.indexOf('windows') !== -1) {
    classname = 'windows';
    if (nameLowerCase.indexOf('windows 10') !== -1 || nameLowerCase.indexOf('windows 8') !== -1) {
      classname = 'windows8';
    }
  } else if (nameLowerCase.indexOf('mac') !== -1) {
    classname = 'mac';
  } else if (nameLowerCase.indexOf('android') !== -1) {
    classname = 'android';
  } else if (nameLowerCase.indexOf('ios') !== -1) {
    classname = 'ios';
  } else if (nameLowerCase.indexOf('linux') !== -1) {
    classname = 'linux';
  } else {
    classname = 'default';
  }

  return (
    <span data-tip={tooltip} data-for="chatContainer">
      <Icon className={classnames(style[`os-${classname}`], style.iconOffsetDown)} />
    </span>
  );
};

export default OS;

OS.propTypes = {
  name: React.PropTypes.string,
};




