import React from 'react';
import * as visitorIdentityType from '../../constants/enums/visitorType';

const Avatar = (props) => {
  const identityType = props.identityType;
  let avatarLink= require('./images/visitor.png'); // eslint-disable-line
  let tooltip = 'Visitor';
  if (props.isContactOrUserOpen) {
    switch (identityType) {
      case visitorIdentityType.visitorIdentityTypeUser: {
        avatarLink= require('./images/user.png'); // eslint-disable-line
        tooltip = 'User';
        break;
      }
      case visitorIdentityType.visitorIdentityTypeContact: {
        avatarLink= require('./images/contact.png'); // eslint-disable-line
        tooltip = 'Contact';
        break;
      }
      case visitorIdentityType.visitorIdentityTypeVisitor: {
        avatarLink= require('./images/visitor.png'); // eslint-disable-line
        tooltip = 'Visitor';
        break;
      }
      default: {
        avatarLink= require('./images/visitor.png'); // eslint-disable-line
        tooltip = '';
        break;
      }
    }
  }
  return (
    <img
      data-tip={tooltip} data-for="chatContainer"
      src={props.avatarLink
      ? props.avatarLink :
      avatarLink}
      alt=""
    />
  );
};

export default Avatar;

Avatar.propTypes = {
  avatarLink: React.PropTypes.string,
  identityType: React.PropTypes.number,
  isContactOrUserOpen: React.PropTypes.bool,
};



