import React from 'react';
// import classnames from 'classnames';

import Modal from '../Modal';
// import ModalStyle from '../Modal.css';
import TextArea from '../../TextArea/TextArea';
// import Button from '../../Button/Button';
import RadioButton from '../../RadioButton/RadioButton';
import { shouldComponentUpdateGen } from '../../../utils/helper';

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'ModalBanChat',
  ['visitorId', 'realVisitorId', 'visitorIP', 'ifOpen'],
  ['banType', 'comment']
);

class ModalBanChat extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      banType: '0',
      comment: '',
    };
    this.handleRadioChange = this.handleRadioChange.bind(this);
    this.handleTextAreaChange = this.handleTextAreaChange.bind(this);
    this.handleBanChat = this.handleBanChat.bind(this);
  }
  shouldComponentUpdate(nextProps, nextState) {
    // if (nextProps.ifOpen === false && this.props.ifOpen === false) return false;
    return shouldComponentUpdateHelper(this.props, nextProps, this.state, nextState);
  }
  handleRadioChange(event) {
    this.setState({
      banType: event.target.value,
    });
  }
  handleTextAreaChange(event) {
    this.setState({
      comment: event.target.value,
    });
  }
  handleBanChat() {
    const banInfo = {
      banType: this.state.banType,
      ip: this.props.visitorIP,
      comment: this.state.comment,
    };
    this.props.onBanChat(this.props.visitorId, banInfo);
    this.props.closeModal();
  }
  render() {
    const id = this.props.realVisitorId;
    const text = id > 0 ? ` (Visitor Id: ${id})` : '';
    return (
      <Modal
        closeModal={this.props.closeModal}
        title="Ban Visitor"
        actions={{
          okProps: { text: 'OK', eventClick: this.handleBanChat },
          cancelProps: { text: 'Cancel' },
        }}
      >
        <RadioButton
          name="banvisitor"
          radioOption={[
            {
              text: `Ban visitor from this Visitor Id.${text}`,
              value: '0',
              checked: this.state.banType === '0',
            },
            {
              text: `Ban visitor from this IP. (IP: ${this.props.visitorIP})`,
              value: '1',
              checked: this.state.banType === '1',
            },
          ]}
          // selectedVal={this.state.banType}
          handleChange={this.handleRadioChange}
        />
        <div>Comment:</div>
        <TextArea
          meta={{}}
          input={{ onChange: this.handleTextAreaChange }}
          name="bancomment"
          cols="40"
          rows="5"
        />

      </Modal>
    );
  }

}

export default ModalBanChat;

ModalBanChat.propTypes = {
  // ifOpen: React.PropTypes.bool.isRequired,
  onBanChat: React.PropTypes.func.isRequired,
  closeModal: React.PropTypes.func.isRequired,
  visitorId: React.PropTypes.number,
  realVisitorId: React.PropTypes.number,
  visitorIP: React.PropTypes.string,
};





