import React from 'react';
import classnames from 'classnames';
import Link from 'src/components/Link/Link';
import GrayButton from '../../Button/Button';
import chatHeaderStyle from '../ChatHeader/ChatHeader.css';
import commonStyle from '../../CommonResources/common.css';
import { PromoteToUser, PromoteToContact } from 'src/constants/enums/promote';

import ModalStandbyNotice from '../../Modal/StandbyNotice/StandbyNotice';

class Promote extends React.Component {
  constructor() {
    super();
    this.state = {
      isOpen: false,
      isOpenModalStandbyNotice: false,
    };
    this.handleClick = this.handleClick.bind(this);
    this.toggle = this.toggle.bind(this);
    this.onPromote = this.onPromote.bind(this);
    this.popUpStandbyWindow = this.popUpStandbyWindow.bind(this);
    this.closeStandbyWindow = this.closeStandbyWindow.bind(this);
  }
  componentDidMount() {
    document.addEventListener('click', this.handleClick, false);
  }
  componentWillUnmount() {
    document.removeEventListener('click', this.handleClick, false);
  }
  onPromote(promoteType) {
    this.setState(
      { isOpen: false }
    );
    if (this.props.ifUseMainServer) {
      this.props.onPromote(this.props.visitorId, promoteType);
    } else {
      this.popUpStandbyWindow();
    }
  }
  popUpStandbyWindow() {
    this.setState(
      { isOpenModalStandbyNotice: true }
    );
  }
  closeStandbyWindow() {
    this.setState(
      { isOpenModalStandbyNotice: false }
    );
  }
  handleClick(event) {
    const container = this.container;
    if (container && !container.contains(event.target) && this.state.isOpen) {
      this.setState({ isOpen: false });
    }
  }
  toggle() {
    this.setState({ isOpen: !this.state.isOpen });
  }
  render() {
    return (
      <div ref={(element) => { this.container = element; }}>
        <GrayButton
          text="Promote"
          className={chatHeaderStyle.btnAction}
          onClick={this.toggle}
          disabled={this.props.disabled}
        />
        <ul
          className={
            classnames(commonStyle.popupList, this.state.isOpen && commonStyle.open)}
        >
          <li>
            <Link onClick={() => this.onPromote(PromoteToContact)}>Promote to Contact</Link>
          </li>
          {
            this.props.preChatEmail !== '' ?
              <li>
                <Link onClick={() => this.onPromote(PromoteToUser)}>Promote to User</Link>
              </li>
              :
              ''
          }
        </ul>
        {this.state.isOpenModalStandbyNotice && <ModalStandbyNotice
          ifOpen={this.state.isOpenModalStandbyNotice}
          closeModal={this.closeStandbyWindow}
          text="Live Chat is running on the secondary server. We're sorry but you're
          unable to perform this action now. Please try again after Live Chat goes back
          to the primary server."
        />}
      </div>
    );
  }
}

export default Promote;

Promote.propTypes = {
  visitorId: React.PropTypes.number.isRequired,
  onPromote: React.PropTypes.func.isRequired,
  preChatEmail: React.PropTypes.string,
  disabled: React.PropTypes.bool,
  ifUseMainServer: React.PropTypes.bool.isRequired,
};



