import React from 'react';
import classnames from 'classnames';
import Link from 'src/components/Link/Link';
import * as enumFontSize from 'src/constants/enums/fontSize';
import Button from 'src/components/Button/Button';
import style from '../Icon.css';
import chatActionStyle from '../../ChatSection/ChatAction/ChatAction.css';
import Icon from '../../Icon/Icon';
import { icons as enums } from '../../../constants/enums';

class Font extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isOpen: false,
    };
    this.toggle = this.toggle.bind(this);
    this.selectSmall = this.onSelect.bind(this, enumFontSize.small);
    this.selectMiddle = this.onSelect.bind(this, enumFontSize.middle);
    this.selectLarge = this.onSelect.bind(this, enumFontSize.large);
    this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount() {
    document.addEventListener('click', this.handleClick, false);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.handleClick, false);
  }

  onSelect(type) {
    this.setState({ isOpen: false });
    this.props.onFontSelect(type);
  }

  handleClick(event) {
    const container = this.container;
    if (this.state.isOpen && container && !container.contains(event.target)) {
      this.setState({ isOpen: false });
    }
  }

  toggle() {
    this.setState({
      isOpen: !this.state.isOpen,
    });
  }

  render() {
    const { isOpen } = this.state;
    const { selectedFontSize } = this.props;
    const isSmall = selectedFontSize === enumFontSize.small;
    const isMiddle = selectedFontSize === enumFontSize.middle;
    const isLarge = selectedFontSize === enumFontSize.large;
    return (
      <div ref={(element) => { this.container = element; }}>
        <Button
          type="noborder"
          className={
            classnames(
                  chatActionStyle.chatActionIcon,
                  isOpen && chatActionStyle.iconActive
                )
          }
          onClick={this.toggle}
          data-tip="Adjust font size of chat messages." data-for="chatContainer"
        >
          <Icon type={enums.font} />
        </Button>
        <div
          className={classnames(style.cover, isOpen && style.coverOpen)}
          onClick={this.toggle}
        />
        <ul className={classnames(style.fontList, isOpen && style.fontOpen)}>
          <li className={isSmall && style.fontActive}>
            <Link onClick={this.selectSmall}>Small</Link>
          </li>
          <li className={isMiddle && style.fontActive} >
            <Link onClick={this.selectMiddle}>Middle</Link>
          </li>
          <li className={isLarge && style.fontActive} >
            <Link onClick={this.selectLarge}>Large</Link>
          </li>
        </ul>
      </div>
    );
  }
}

Font.propTypes = {
  selectedFontSize: React.PropTypes.string.isRequired,
  onFontSelect: React.PropTypes.func.isRequired,
};

export default Font;



