import { connect } from 'react-redux';
import NoChat from '../components/ChatItem/NoChat';
import { showVisitor } from '../actions/ui';

const mapDispatchToProps = dispatch => ({
  goToVisitors() {
    dispatch(showVisitor());
  },
});
export default connect(undefined, mapDispatchToProps)(NoChat);




