import React, { Component } from 'react';
import classnames from 'classnames';
import Upload from 'rc-upload';
import * as _ from 'lodash';
import style from '../Icon.css';
import Icon from '../../Icon/Icon';
import { icons as enums } from '../../../constants/enums';
import { formatAgentChatDownloadUrl, shouldComponentUpdateGen } from '../../../utils/helper';

import FileUploadProgress from '../../FileUploadProgress/FileUploadProgress';

const shouldComponentUpdateHelper = shouldComponentUpdateGen(
  'SendFile',
  ['disabled', 'files']
);

const emptyConfig = { filename: '', guid: 0, percentage: 0 };
const getFile = (files) => {
  const last = files[files.length - 1];
  if (!last || last.percentage >= 100) return emptyConfig;
  return last;
};

class SendFile extends Component {
  constructor(props) {
    super(props);
    this.upload = null;
    this.file = null;
    this.guid = 0;

    this.showErrorMessage = props.showErrorMessage.bind(null, props.chatId);

    this.uploadConfig = {
      action: props.siteInfo.sendFileUploadUrl,
      multiple: false,
      disabled: props.disabled,
      beforeUpload: this.beforeUpload,
      onStart: _.noop,
      onSuccess: this.onSuccess,
      onProgress: this.onProgress,
      onError: this.onError,
    };
  }

  componentWillReceiveProps(nextProps) {
    this.uploadConfig.action = nextProps.siteInfo.sendFileUploadUrl;
    this.uploadConfig.disabled = nextProps.disabled;
    if (nextProps.chatId !== this.props.chatId) {
      this.showErrorMessage = this.props.showErrorMessage.bind(null, nextProps.chatId);
    }
  }

  shouldComponentUpdate(nextProps) {
    return shouldComponentUpdateHelper(this.props, nextProps);
  }

  beforeUpload = (file) => {
    const siteInfo = this.props.siteInfo;
    if (file.size <= siteInfo.maxFileSize * 1024 * 1024) {
      if (this.props.files.length >= siteInfo.maxFileNum) {
        this.showErrorMessage([
          'Error sending file: The max number of files you can send/receive during this',
          `chat session is ${siteInfo.maxFileNum}.`,
        ].join(' '));
        return false;
      }
    } else {
      this.showErrorMessage(`Error sending file: The max file size is ${siteInfo.maxFileSize} MB.`);
      return false;
    }
    const shouldUpload = this.props.beforeUpload ? this.props.beforeUpload(file) : true;
    if (shouldUpload) {
      this.guid = Date.now();
      this.props.onStart(
        this.props.chatId,
        file.name,
        this.guid
      );
      this.file = file;
    }
    return shouldUpload;
  }

  onProgress = (step, file) => {
    this.props.onProgress(
      this.props.chatId,
      step.percent === 100 ? 100 : parseFloat(step.percent.toFixed(1)),
      file.name,
      this.guid
    );
  }

  onSuccess = (rsp, file) => {
    this.file = null;
    if (rsp && (rsp === 'success' || rsp.isSuccess)) {
      this.props.onSuccess(
        this.props.chatId,
        file.name,
        this.guid,
        this.props.siteInfo,
        this.getDownloadUrl(rsp.guid, file.name),
        rsp.guid
      );
      return;
    }
    const errorMsg = rsp.errorMessage || rsp;
    this.showErrorMessage(`Error sending file: ${errorMsg}`);
    this.props.onCancel(this.props.chatId, file.name, this.guid);
  }

  onError = (e, file) => { // todo
    this.file = null;
    this.props.onCancel(file.name, this.guid);
  }

  getDownloadUrl = (guid, name) => {
    if (!this.props.siteInfo) return '';
    const { downloadUrl, sessionId, siteId, privateMessage } = this.props.siteInfo;
    return formatAgentChatDownloadUrl(downloadUrl,
                            sessionId,
                            siteId,
                            privateMessage,
                            guid,
                            name);
  }

  handleAbortUpload = (file, filename) => {
    if (this.upload && this.file === file) this.upload.abort(file);
    this.file = null;
    this.props.onCancel(this.props.chatId, filename, this.guid);
  }

  render() {
    this.uploadConfig.disabled = this.props.disabled;
    const { filename, percentage, guid } = getFile(this.props.files);
    this.guid = guid;
    if (!this.props.disabled) {
      if (percentage !== 100 && percentage !== 0) {
        this.uploadConfig.disabled  = true;
      } else {
        this.uploadConfig.disabled  = false;
      }
    }
    return (
      <div>
        <FileUploadProgress
          file={this.file}
          fileName={filename}
          fileUploadProgressNumber={percentage}
          handleAbortUpload={this.handleAbortUpload}
        />
        <span
          className={classnames(style.fileupload, this.props.className)}
          data-tip="Send a file from your local machine." data-for="chatContainer"
        >
        <Upload
          ref={(element) => { this.upload = element; }}
          {...this.uploadConfig}
        >
          <Icon type={enums.sendFile} />
        </Upload>
        </span>
      </div>
    );
  }
}

SendFile.propTypes = {
  siteInfo: React.PropTypes.shape({
    sendFileUploadUrl: React.PropTypes.string,
    downloadUrl: React.PropTypes.string,
    sessionId: React.PropTypes.string,
    siteId: React.PropTypes.number,
    privateMessage: React.PropTypes.number,
    maxFileSize: React.PropTypes.number,
    maxFileNum: React.PropTypes.number,
  }).isRequired,
  className: React.PropTypes.string,
  disabled: React.PropTypes.bool.isRequired,
  beforeUpload: React.PropTypes.func,
  onStart: React.PropTypes.func,
  onSuccess: React.PropTypes.func,
  onProgress: React.PropTypes.func,
  onError: React.PropTypes.func,
  showErrorMessage: React.PropTypes.func,
  chatId: React.PropTypes.number.isRequired,
  files: React.PropTypes.arrayOf(React.PropTypes.shape({
    filename: React.PropTypes.string.isRequired,
    percentage: React.PropTypes.number.isRequired,
  })).isRequired,
};

export default SendFile;



