import React from 'react';
// import ReactModal from 'react-modal';
import Modal from '../Modal';
// import ModalStyle from '../Modal.css';
// import Button from '../../Button/Button';

const ModalLeaveChat = props => (
  <Modal
    closeModal={props.closeModal}
    title="Comm100 Live Chat"
    actions={{
      okProps: { text: 'Yes', eventClick: props.onLeaveChat },
      cancelProps: { text: 'No' },
    }}
  >
    <div>
      Are you sure you want to leave this chat?
    </div>
  </Modal>
);

export default ModalLeaveChat;

ModalLeaveChat.propTypes = {
  // ifOpen: React.PropTypes.bool.isRequired,
  onLeaveChat: React.PropTypes.func.isRequired,
  closeModal: React.PropTypes.func.isRequired,
};




