import React from 'react';
import GrayButton from '../../Button/Button';
import chatHeaderStyle from '../ChatHeader/ChatHeader.css';

const CloseChat = props => (
  <div>
    <GrayButton
      text="Close Chat Window"
      className={chatHeaderStyle.btnAction}
      onClick={() => props.onCloseChat(props.chatId, props.status, props.visitorId)}
    />
  </div>
);

export default CloseChat;


CloseChat.propTypes = {
  onCloseChat: React.PropTypes.func.isRequired,
  chatId: React.PropTypes.number,
  visitorId: React.PropTypes.number,
  status: React.PropTypes.number,
};



